import React from 'react';
import { 
    Typography,
    Box,
    Divider,
} from '@mui/material';
import BoxImg from '../assets/images/box.png';
import CashpriceImg from '../assets/images/Cashprice.png';

const Cashprice = () => {
    return (
        <Box sx={{
            position: 'relative',
            backgroundColor: '#fff',
            boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
            borderRadius: '8px',
            mt: '54px',
            mb: '24px',
        }}>
            <Box
                component="img"
                src={BoxImg}
                alt=""
                sx={{
                    position: 'absolute',
                    right: '24px',
                    top: '-54px',
                    width: {xs: 200, sm: 257, md: 200, lg: 257}
                }}
            />
            <Typography sx={{
               fontFamily: 'Poppins-Medium',
               fontSize: '15px',
               lineHeight: '22px',
               letterSpacing: '0.01em',
               color: '#192A3E',
               p: '16px 24px'
            }}>
               Cashprice
            </Typography>
            <Divider />
            <Box sx={{ 
                p: '16px 24px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
            }}>
                <Typography
                    sx={{
                        textAlign: 'center',
                        fontFamily: 'Poppins-SemiBold',
                        fontSize: {xs: 40, sm: 51, md: 40, lg: 51},
                        lineHeight: '76px',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                        mr: '8px'
                    }}
                >
                    58.512,60
                </Typography>
                <Box 
                    component="img"
                    src={CashpriceImg}
                    alt=""
                    width={37}
                />
            </Box>
        </Box>
    )
}

export default Cashprice;