import React, { useState, forwardRef } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Box,
    Slide
} from '@mui/material';
import PelfIcon from '../assets/images/pelf.png';

const useStyles = makeStyles(theme => ({
    button: {
        background: '#BF0C1A!important',
        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)!important',
        fontFamily: 'Poppins-SemiBold!important',
        fontSize: '12px!important',
        letterSpacing: '0.01em!important',
        textTransform: 'capitalize!important',
        minHeight: '37px',
    },
    pelfImage: {
        display: 'block',
        margin: '12px auto 16px'
    },
    input: {
        color: '#CD1322',
        fontFamily: 'Poppins-SemiBold',
        fontSize: '12px',
        letterSpacing: '0.01em',
        textTransform: 'capitalize',
        border: '1px solid #CD1322',
        borderRadius: '4px',
        outline: 'none',
        textAlign: 'center',
        marginRight: '12px',
        width: '30%',
    }
}));

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const StakeModal = ({open, onClose}) => {
    const classes = useStyles();
    const [number, setNumber] = useState(0);
    return (
        <Dialog 
            onClose={onClose} 
            open={open} 
            TransitionComponent={Transition}
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper sx={{
                padding: '46px 24px 24px'
            }}>
                <Typography
                    sx={{
                        fontFamily: 'Poppins-Bold',
                        fontSize: '15px',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}
                >
                    Stack to Boost your Egg Production
                </Typography>
                <Typography
                    sx={{
                        fontSize: '12px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}
                >
                    Stack <b>250 MATIC</b> to increase by your Egg Rate.
                </Typography>
                <Typography
                    sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5'
                    }}
                >
                    (1 minute / MATIC)
                </Typography>
                <img className={classes.pelfImage} src={PelfIcon} alt="" />
                <Typography
                    sx={{
                        fontSize: '14px',
                        lineHeight: '21px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        marginBottom: '24px',
                        fontFamily: 'Poppins-Regular',
                        "& b": {
                            fontFamily: 'Poppins-SemiBold',
                        }
                    }}
                >
                    <b>0</b>/250
                </Typography>
                <Box sx={{
                    display: 'flex',
                    marginBottom: '12px'
                }}>
                    <input
                        value={number}
                        onChange={(e) => setNumber(e.target.value)}
                        className={classes.input}
                    />
                    <Button 
                        variant='contained'
                        className={classes.button}
                        onClick={onClose}
                        sx={{
                            flexGrow: '1'
                        }}
                    >
                        Stack
                    </Button>
                </Box>
                
                <Button
                    variant='outlined'
                    fullWidth
                    onClick={onClose}
                    sx={{
                        color: '#CD1322',
                        fontFamily: 'Poppins-SemiBold',
                        fontSize: '12px',
                        minHeight: '37px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        borderColor: '#CD1322',
                        "&:hover": {
                            borderColor: '#CD1322',
                        }
                    }}
                >
                    Cancel
                </Button>
            </Paper>
        </Dialog>
    );
}

export default StakeModal;