import React, { forwardRef, useEffect, useState } from 'react';
import { 
    Dialog,
    Paper,
    Slide,
    Typography,
    Box,
    Button
} from '@mui/material';
import { makeStyles } from '@mui/styles';
import Timer from './Timer';
import IlluCornoids from '../assets/images/illu_cornoids4-24.png';
import PelfImage from '../assets/images/pelf.png';

const useStyles = makeStyles(theme => ({
    input: {
        color: '#105157',
        fontFamily: 'Poppins-SemiBold',
        fontSize: '12px',
        letterSpacing: '0.01em',
        textTransform: 'capitalize',
        border: '1px solid #105157',
        borderRadius: '4px',
        outline: 'none',
        textAlign: 'center',
        marginRight: '12px',
        width: '30%',
    }
}));

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const FeedingModal = ({open, onClose}) => {
    const classes = useStyles();
    const [isActive, setIsActive] = useState(false);
    const [showCornoids, setShowCornoids] = useState(false);
    const [number, setNumber] = useState(0);
    const [isStart, setIsStart] = useState(false);
    useEffect(() => {
        if (open) setIsStart(true)
    }, [open])
    return (
        <Dialog 
            onClose={onClose} 
            open={open} 
            TransitionComponent={Transition}
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper sx={{
                p: '46px 24px 24px'
            }}>
                <Typography sx={{
                   fontSize: 15,
                   fontFamily: 'Poppins-Bold',
                   lineHeight: '22px',
                   textAlign: 'center',
                   letterSpacing: '0.01em',
                   color: '#192A3E',
                }}>
                    Stack to Boost your Training Timings
                </Typography>
                <Typography sx={{
                    fontSize: 12,
                    fontFamily: 'Poppins-Regular',
                    lineHeight: '18px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: '#4C5862',
                    mb: '16px'
                }}>
                    Stack <b>250 MATIC</b> to increase your timings <br />
                    for unstacking
                </Typography>
                <Box sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    mb: '16px'
                }}>
                    <Timer isActive={isActive} timeFrame={80} setShowCornoids={setShowCornoids} />
                </Box>
                <Button
                    fullWidth
                    variant='contained'
                    sx={{
                        background: '#BF0C1A!important',
                        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                        fontFamily: "Poppins-SemiBold",
                        fontSize: '12px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        minHeight: '37px',
                    }}
                    onClick={() => {
                        setIsActive(isActive => !isActive);
                        setIsStart(false);
                    }}
                >
                    {isStart ? 'Start Feed' : isActive ? 'Stop Training' : 'Continue'}
                </Button>
                {isStart && <Box>
                    <Typography sx={{
                        fontSize: 12,
                        position: 'relative',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#A6ACB1',
                        my: '16px',
                        "&:before": {
                            content: "''",
                            position: 'absolute',
                            top: 'calc(50% - 1px)',
                            height: '2px',
                            background: '#EBEFF2',
                            left: '0',
                            width: 'calc(50% - 20px)',
                        },
                        "&:after": {
                            content: "''",
                            position: 'absolute',
                            top: 'calc(50% - 1px)',
                            height: '2px',
                            background: '#EBEFF2',
                            right: '0',
                            width: 'calc(50% - 20px)',
                        }
                    }}>
                        OR
                    </Typography>
                    <Typography sx={{
                        fontSize: 12,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}>
                        Use your CORNS to feed your Rooster
                    </Typography>
                    <Typography sx={{
                        fontSize: 10,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5'
                    }}>
                        (Lorem ipsum doler)
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'center',
                        my: '38px',
                    }}>
                        <img src={PelfImage} alt="" />
                    </Box>
                    <Box sx={{
                        display: 'flex',
                        marginBottom: '12px'
                    }}>
                        <input
                            value={number}
                            onChange={(e) => setNumber(e.target.value)}
                            className={classes.input}
                        />
                        <Button 
                            variant='contained'
                            onClick={onClose}
                            sx={{
                                flexGrow: '1',
                                background: '#105157!important',
                                boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                                fontFamily: 'Poppins-SemiBold',
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                minHeight: '37px',
                            }}
                        >
                            Feed with CORNS
                            <Typography sx={{
                                fontSize: '10px',
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '15px',
                                letterSpacing: '0.01em',
                                color: '#FFFFFF',
                                ml: '4px'
                            }}>
                                (Max. 900)
                            </Typography>
                        </Button>
                    </Box>
                    
                    <Button
                        variant='outlined'
                        fullWidth
                        onClick={onClose}
                        sx={{
                            color: '#CD1322',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            minHeight: '37px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            borderColor: '#CD1322',
                            "&:hover": {
                                borderColor: '#CD1322',
                            }
                        }}
                    >
                        Cancel
                    </Button>
                </Box>}
                {showCornoids && <Box>
                    <Typography sx={{
                        fontSize: 12,
                        position: 'relative',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#A6ACB1',
                        my: '16px',
                        "&:before": {
                            content: "''",
                            position: 'absolute',
                            top: 'calc(50% - 1px)',
                            height: '2px',
                            background: '#EBEFF2',
                            left: '0',
                            width: 'calc(50% - 20px)',
                        },
                        "&:after": {
                            content: "''",
                            position: 'absolute',
                            top: 'calc(50% - 1px)',
                            height: '2px',
                            background: '#EBEFF2',
                            right: '0',
                            width: 'calc(50% - 20px)',
                        }
                    }}>
                        OR
                    </Typography>
                    <Typography sx={{
                        fontSize: 12,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}>
                        Buy a shot of Cornoids for perfect training stats
                    </Typography>
                    <Typography sx={{
                        fontSize: 10,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5'
                    }}>
                        (Get your XP and your energy)
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'center'
                    }}>
                        <img src={IlluCornoids} alt="" />
                    </Box>
                    <Button
                        fullWidth
                        variant='contained'
                        onClick={onClose}
                        sx={{
                            background: '#BF0C1A!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: "Poppins-SemiBold",
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mb: '12px'
                        }}
                    >
                        Buy Cornoids Shot
                    </Button>
                    <Button
                        variant='outlined'
                        fullWidth
                        onClick={onClose}
                        sx={{
                            color: '#CD1322',
                            fontFamily: "Poppins-SemiBold",
                            fontSize: '12px',
                            minHeight: '37px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            borderColor: '#CD1322',
                            "&:hover": {
                                borderColor: '#CD1322',
                            }
                        }}
                    >
                        Cancel
                    </Button>
                </Box>}
            </Paper>
        </Dialog>
    );
}

export default FeedingModal;