import React, { Fragment, useState, useEffect, forwardRef } from 'react';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Slide,
    Box,
    IconButton,
    Divider,
    LinearProgress
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import EggIcon from '../assets/images/egg.svg';
import DisabledEggIcon from '../assets/images/disabled-egg.svg';
import ChickenImage from '../assets/images/chicken.png';
import Diamond from '../assets/images/diamond.svg';

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const CardItem = ({icon, title, subText, color, value, disabled=false}) => {
    return (
        <Fragment>
            <Box sx={{
                display: 'flex',
                alignItems: 'center'
            }}>
                <img src={icon} alt="" />
                <Typography
                    sx={{
                        fontSize: '12px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        letterSpacing: '0.01em',
                        color: disabled ? '#CBD4DA' : '#4C5862',
                        ml: '8px'
                    }}
                >
                    {title}
                    <Typography
                        component="span"
                        sx={{
                            fontFamily: 'Poppins-Bold',
                            fontSize: 12,
                            lineHeight: '21px',
                            letterSpacing: '0.01em',
                            color: disabled ? '#CBD4DA' : '#4C5862',
                            ml: '4px'
                        }}
                    >
                        {disabled ? '??????' : subText}
                    </Typography>
                </Typography>
                
            </Box>
            <LinearProgress 
                variant="determinate" 
                value={disabled ? 0 : value}
                sx={{
                    height: '6px!important',
                    borderRadius: 3,
                    margin: '6px 0',
                    backgroundColor: '#EBEFF2!important',
                    "& > span": {
                        borderRadius: 3,
                        backgroundColor: color
                    }
                }}
            />
        </Fragment>
    )
}

const BreedingItem = ({selected, resting, onSelect}) => {
    const selectBreed = () => {
        if (!resting) {
            onSelect()
        }
    }
    return (
        <Box 
            sx={{
                cursor: 'pointer',
                position: 'relative',
                boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                borderRadius: '4px',
                width: { xs: 'calc(50% - 12px)', sm: 'calc(33% - 16px)' },
                pt: '14px',
                pb: '20px',
                mb: '16px',
                opacity: resting ? '0.5' : '1',
            }}
            onClick={selectBreed}
        >
            <Box sx={{
                position: 'absolute',
                top: '-2px',
                right: '-2px',
                width: '92px',
                height: '92px',
                overflow: 'hidden',
                "&:before": {
                    content: "''",
                    backgroundColor: resting ? "#C4C4C4" : selected ? "#DE7588" : null,
                    position: 'absolute',
                    top: '0px',
                    right: '47px',
                    width: '40px',
                    height: '2px',
                    borderRadius: '2px 0 0 0',

                },
                "&:after": {
                    content: "''",
                    backgroundColor: resting ? "#C4C4C4" : selected ? "#DE7588" : null,
                    position: 'absolute',
                    top: '46px',
                    right: '1px',
                    width: '40px',
                    height: '2px',
                    transformOrigin: 'right',
                    transform: 'rotate(-90deg)',
                    borderRadius: '0 0px 0 2px',
                }
            }}>
                <Box sx={{
                        position: 'absolute',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        fontFamily: 'Poppins-Medium',
                        background: resting ? "#C4C4C4" : selected ? "#DE7588" : null,
                        right: '-49px',
                        top: '15px',
                        fontSize: '11px',
                        color: '#fff',
                        transform: 'rotate(45deg)',
                        width: '160px',
                        height: '32px',
                }}>
                    {selected ? "Selected" : resting ? "Resting..." : null}
                </Box>
            </Box>
            <Box sx={{
                display: 'flex',
                justifyContent: 'space-between',
                mb: '10px',
                px: '12px'
            }}>
                <Typography
                    sx={{
                        fontSize: '15px',
                        fontFamily: 'Poppins-Medium',
                        lineHeight: '22px',
                        letterSpacing: '0.01em',
                        color: '#323C47',
                    }}
                >
                    #00000
                </Typography>
                <Box sx={{
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    <Typography
                        sx={{
                            fontSize: '14px',
                            lineHeight: '21px',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            marginRight: '4px',
                            fontFamily: 'Poppins-Bold',
                        }}
                    >
                        3
                    </Typography>
                    <img src={Diamond} alt="" />
                </Box>
            </Box>
            <Box sx={{
                aspectRatio: '1',
                backgroundColor: '#64EC4E'
            }}></Box>
            <Box sx={{ p: '16px 12px 0' }}>
                <CardItem 
                    icon={EggIcon} 
                    title="Egg Rarity :" 
                    subText="Common"
                    color="#FEB12F"
                    value={20}
                />
            </Box>
        </Box>
    )
}

const BreedingModal = ({open, onClose}) => {
    const [confirm, setConfirm] = useState(false);
    const [selectId, setSelectId] = useState(null);
    const closeModal = () => {
        setSelectId(null);
        setConfirm(false);
        onClose();
    }

    useEffect(() => {
        if (selectId !== null) {
            setConfirm(true);
        } else {
            setConfirm(false);
        }
    }, [selectId, setConfirm])
    return (
        <Dialog 
            TransitionComponent={Transition}
            onClose={closeModal} 
            open={open} 
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 636,
                    width: '100%'
                }
            }}
        >
            <Paper>
                <Box sx={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    p: '8px 24px'
                }}>
                    <Typography sx={{
                        fontSize: '15px',
                        fontFamily: 'Poppins-Medium',
                        lineHeight: '22px',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}>
                        Breeding
                    </Typography>
                    <IconButton size="small" onClick={closeModal}>
                        <CloseIcon />
                    </IconButton>
                </Box>
                <Divider />
                <Box sx={{
                    p: '42px 24px 24px'
                }}>
                    <Typography
                        sx={{
                            fontFamily: "Poppins-Bold",
                            fontSize: '15px',
                            lineHeight: '22px',
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#192A3E',
                            mb: '4px'
                        }}
                    >
                        Pick a Rooster
                    </Typography>
                    <Typography
                        sx={{
                            fontSize: '12px',
                            lineHeight: '18px',
                            fontFamily: "Poppins-Regular",
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            maxWidth: 400,
                            mx: 'auto',
                            mb: '24px'
                        }}
                    >
                        Pick a rooster to fecondate your chicken <b>#10001</b> and mint a new Egg according to the Egg Quality.
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        mb: '24px'
                    }}>
                        <Box sx={{
                            boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                            borderRadius: '4px',
                            width: 'calc(50% - 12px)',
                            pt: '14px',
                            pb: '20px'
                        }}>
                            <Typography sx={{
                                fontSize: '15px',
                                fontFamily: 'Poppins-Medium',
                                lineHeight: '22px',
                                letterSpacing: '0.01em',
                                color: '#323C47',
                                mb: '10px',
                                px: '12px'
                            }}>
                                #10001
                            </Typography>
                            <Box sx={{
                                aspectRatio: '1',
                                backgroundColor: '#64EC4E'
                            }}></Box>
                            <Box sx={{
                                p: '16px 24px 0'
                            }}>
                                <CardItem 
                                    icon={EggIcon} 
                                    title="Egg Rarity :" 
                                    subText="Common"
                                    color="#FEB12F"
                                    value={10}
                                />
                            </Box>
                        </Box>
                        <Box sx={{
                            boxShadow: confirm ? '0px 2px 10px rgba(0, 0, 0, 0.08)' : '',
                            border: confirm ? '' : '3px dashed #EBEFF2',
                            borderRadius: '4px',
                            width: 'calc(50% - 12px)',
                            pt: '14px',
                            pb: '20px'
                        }}>
                            <Box sx={{
                                display: 'flex',
                                justifyContent: confirm ? 'space-between' : 'center',
                                mb: '10px',
                                px: '12px'
                            }}>
                                {confirm ? (
                                    <Fragment>
                                        <Box
                                            sx={{
                                                display: 'flex',
                                                alignItems: 'center',
                                            }}
                                        >
                                            <Typography
                                                sx={{
                                                    fontSize: '15px',
                                                    fontFamily: 'Poppins-Medium',
                                                    lineHeight: '22px',
                                                    letterSpacing: '0.01em',
                                                    color: '#323C47',
                                                }}
                                            >
                                                #00000
                                            </Typography>
                                        </Box>
                                        <Box sx={{
                                            display: 'flex',
                                            alignItems: 'center'
                                        }}>
                                            <Typography
                                                sx={{
                                                    fontSize: '14px',
                                                    lineHeight: '21px',
                                                    letterSpacing: '0.01em',
                                                    color: '#4C5862',
                                                    marginRight: '4px',
                                                    fontFamily: 'Poppins-Bold',
                                                }}
                                            >
                                                3
                                            </Typography>
                                            <img src={Diamond} alt="" />
                                        </Box>
                                    </Fragment>
                                ) : (
                                    <Typography
                                        sx={{
                                            fontSize: '15px',
                                            fontFamily: 'Poppins-Medium',
                                            lineHeight: '22px',
                                            letterSpacing: '0.01em',
                                            color: '#CBD4DA',
                                        }}
                                    >
                                        #00000
                                    </Typography>
                                )}
                            </Box>
                            {confirm ? (
                                <Box sx={{
                                    aspectRatio: '1',
                                    backgroundColor: '#64EC4E'
                                }}></Box>
                            ) : (
                                <Box sx={{
                                    aspectRatio: '1',
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    alignItems: 'center'
                                }}>
                                    <img src={ChickenImage} alt="" />
                                    <Typography sx={{
                                        fontSize: '12px',
                                        fontFamily: 'Poppins-Regular',
                                        lineHeight: '18px',
                                        textAlign: 'center',
                                        letterSpacing: '0.01em',
                                        color: '#CBD4DA',
                                        mt: '8px'
                                    }}>
                                        Pick a Rooster
                                    </Typography>
                                </Box>
                            )}
                            <Box sx={{
                                p: '16px 24px 0'
                            }}>
                                <CardItem 
                                    icon={confirm ? EggIcon : DisabledEggIcon} 
                                    title="Egg Rarity :" 
                                    subText="Rare"
                                    disabled={!confirm}
                                    color="#FEB12F"
                                    value={10}
                                />
                            </Box>
                        </Box>
                    </Box>
                    <Divider />
                    <Box sx={{
                        display: 'flex',
                        flexWrap: 'wrap',
                        justifyContent: 'space-between',
                        m: '24px 0 16px'
                    }}>
                        {Array.from(Array(6).keys()).map((index) => (
                            <BreedingItem 
                                key={index} 
                                selected={selectId === index}
                                resting={index === 2}
                                onSelect={() => setSelectId(index)}
                            />
                        ))}
                    </Box>
                    <Box sx={{
                        display: 'flex',
                        flexDirection: { xs: 'column', sm: 'row' }
                    }}>
                        <Button
                            variant='outlined'
                            onClick={closeModal}
                            sx={{
                                color: '#CD1322',
                                fontFamily: "Poppins-SemiBold",
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                borderColor: '#CD1322',
                                minHeight: '37px',
                                width: { xs: '100%', sm: 137 },
                                mb: { xs: '16px', sm: 0 },
                                mr: '24px',
                                "&:hover": {
                                    borderColor: '#CD1322',
                                }
                            }}
                        >
                            Cancel
                        </Button>
                        <Button 
                            variant='contained'
                            onClick={closeModal}
                            disabled={!confirm}
                            sx={{
                                flexGrow: '1',
                                color: '#fff!important',
                                background: confirm ? '#DE7588!important' : '#C4C4C4!important',
                                boxShadow: '0px 2px 10px rgba(4, 66, 88, 0.08)',
                                fontFamily: "Poppins-SemiBold",
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                minHeight: '37px',
                            }}
                        >
                            Breeding
                            <Typography sx={{
                                fontSize: '12px',
                                fontFamily: 'Poppins-Regular'
                            }}>
                                (10 MATIC)
                            </Typography>
                        </Button>
                    </Box>
                </Box>
            </Paper>
        </Dialog>
    );
}

export default BreedingModal;