import React, { Fragment, useState } from 'react';
import { 
    Typography,
    Box,
    Button,
    Divider
} from '@mui/material';
import ResisterModal from './ResisterModal';
import FarmImage from '../assets/images/farm.png';

const IncomingTournament = () => {
    const [resisterOpen, setResisterOpen] = useState(false);
    return (
        <Fragment>
            <ResisterModal open={resisterOpen} onClose={() => setResisterOpen(false)} />
            <Box sx={{
                position: 'relative',
                backgroundColor: '#fff',
                boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
                borderRadius: '8px',
                marginBottom: '24px'
            }}>
                <Typography sx={{
                    fontFamily: 'Poppins-Medium',
                    fontSize: '15px',
                    lineHeight: '22px',
                    letterSpacing: '0.01em',
                    color: '#192A3E',
                    padding: '16px 24px',
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center'
                }}>
                    Incoming Tournament #2
                    <Typography component="span" sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        color: '#4C5862',
                        opacity: '0.5',
                    }}>
                        Start in 8 hours 40 mins
                    </Typography>
                </Typography>
                <Divider />
                <Box sx={{ padding: '24px 24px 32px' }}>
                    <Box sx={{
                        position: 'relative'
                    }}>
                        <Box
                            component="img"
                            src={FarmImage}
                            alt=""
                            width="100%"
                            sx={{ 
                                borderRadius: '4px',
                                display: 'block'
                            }}
                        />
                        <Box
                            sx={{
                                position: 'absolute',
                                top: 0,
                                left: 0,
                                right: 0,
                                bottom: 0,
                                backgroundColor: 'rgba(0, 0, 0, 0.5)'
                            }}
                        ></Box>
                        <Box
                            sx={{
                                position: 'absolute',
                                top: '50%',
                                left: '50%',
                                transform: 'translate(-50%, -50%)'
                            }}
                        >
                            <Typography sx={{
                                fontSize: 15,
                                lineHeight: '22px',
                                textAlign: 'center',
                                letterSpacing: '0.01em',
                                color: '#FFFFFF',
                                fontFamily: 'Poppins-Bold',
                                whiteSpace: 'nowrap',
                                mb: '4px'
                            }}>
                                Saturday, 12th March
                            </Typography>
                            <Typography
                                sx={{
                                    fontSize: 18,
                                    lineHeight: '27px',
                                    textAlign: 'center',
                                    letterSpacing: '0.01em',
                                    color: '#FFFFFF',
                                    whiteSpace: 'nowrap',
                                    fontFamily: 'Poppins-Regular'
                                }}
                            >
                                6 Days 8 Hours 40 Mins 
                            </Typography>
                        </Box>
                    </Box>
                    <Typography
                        sx={{
                            fontSize: '15px',
                            lineHeight: '22px',
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            margin: '16px 0',
                            fontFamily: 'Poppins-Regular',
                            "&: b": {
                                fontFamily: 'Poppins-SemiBold',
                            }
                        }}
                    >
                        <b>1.325</b> Registered Roosters
                    </Typography>
                    <Button
                        variant="contained"
                        fullWidth
                        sx={{
                            background: '#BF0C1A!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                        }}
                        onClick={() => setResisterOpen(true)}
                    >
                        Register your Roosters (2)
                    </Button>
                </Box>
            </Box>
        </Fragment>
    )
}

export default IncomingTournament;