import React, { useState, useEffect } from 'react';
import { makeStyles } from '@mui/styles';
import {
    Box,
    Container,
    Tabs,
    Tab,
} from '@mui/material';
import { Switch, Route, useHistory, Redirect } from 'react-router-dom';
import Brackets from './Brackets';
import RankingList from './RankingList';
import Results from './Results';
import IncomingTournament from '../../components/IncomingTournament';
import TournamentResults from '../../components/TournamentResults';

const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: '#91b08c',
        padding: '24px',
        marginTop: '64px'
    },
    tabs: {
        background: '#FFFFFF',
        boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
        borderRadius: '8px',
        color: '#CD1322',
        marginBottom: '20px'
    },
    tab: {
        color: '#192A3E!important',
        borderRadius: '8px!important',
        fontSize: '15px!important',
        fontFamily: 'Poppins-Medium!important',
        textTransform: 'capitalize!important'
    },
    activeTab: {
        color: '#fff!important',
        backgroundColor: '#CD1322!important'
    },
    tabPanels: {
        background: '#FFFFFF',
        boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
        borderRadius: '8px',
    }
}));

const Tournament = ({match}) => {
    const classes = useStyles();
    const history = useHistory();
    const [value, setValue] = useState(0);

    useEffect(() => {
        switch(history.location.pathname.split('/')[2]) {
            case 'brackets':
                setValue(0)
                return
            case 'results':
                setValue(1)
                return
            case 'ranking-list':
                setValue(2)
                return
            default:
                return
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [history.location.pathname])

    const handleChange = (e, newValue) => {
        setValue(newValue);
    };

    return (
        <Box className={classes.root}>
            <Container sx={{
                display: 'flex',
                flexWrap: 'wrap'
            }}>
                <Box sx={{
                    maxWidth: { xs: '100%', md: '58%' },
                    width: { xs: '100%', md: '58%' },
                    mb: {xs: '24px', md: '0'},
                    mr: { md: '30px' }
                }}>
                    <Tabs
                        value={value}
                        onChange={handleChange}
                        className={classes.tabs}
                        variant="fullWidth"
                        >
                        <Tab 
                            className={`${classes.tab} ${value === 0 && classes.activeTab}`}
                            label="Brackets"
                            onClick={() => history.push(`${match.path}/brackets`)}
                        />
                        <Tab 
                            className={`${classes.tab} ${value === 1 && classes.activeTab}`}
                            label="Results"
                            onClick={() => history.push(`${match.path}/results`)}
                        />
                        <Tab 
                            className={`${classes.tab} ${value === 2 && classes.activeTab}`}
                            label="Ranking List"
                            onClick={() => history.push(`${match.path}/ranking-list`)}
                        />
                    </Tabs>
                    <Box className={classes.tabPanels}>
                        <Switch>
                            <Route exact path={`${match.path}/brackets`} component={Brackets} />
                            <Route exact path={`${match.path}/results`} component={Results} />
                            <Route exact path={`${match.path}/ranking-list`} component={RankingList} />
                            <Redirect from={`${match.path}`} to={`${match.path}/brackets`} />
                        </Switch>
                    </Box>
                </Box>
                <Box sx={{
                    maxWidth: { xs: '100%', md: 'calc(42% - 30px)' },
                    mt: { xs: 0, md: '68px' }
                }}>
                    <IncomingTournament />
                    <TournamentResults />
                </Box>
            </Container>
        </Box>
    )
}

export default Tournament;