import React, { forwardRef, useState } from 'react';
import { 
    Dialog,
    Paper,
    Typography,
    Slide,
    Box,
    IconButton,
    Button,
    Divider
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import Attributes from './Attributes';
import AttackImage from '../assets/images/attack.svg';
import DefenseImage from '../assets/images/defense.svg';
import StrenghtImage from '../assets/images/Strenght.png';
import ResistanceImage from '../assets/images/Resistance.png';
import UppercutImage from '../assets/images/Uppercut.png';
import DodgeImage from '../assets/images/Dodge.png';
import PoisonedClawsImage from '../assets/images/PoisonedClaws.png';
import BandagesImage from '../assets/images/Bandages.png';
import GoldStar from '../assets/images/gold_star.png';
import WhiteStar from '../assets/images/whitegoldstar.png';
import LockImage from '../assets/images/lock-1.png';
import InfoImage from '../assets/images/icon_24_info.png';

const skillsLists = [
    {
        icon: AttackImage,
        title: 'Attack +',
        text: ['Boost your Attack ', <b>+1</b>, ' point'],
        subText: ['Chance to start fighting', 'with an Attack Buff'],
        level1: '2%',
        level2: '10%',
        level3: '25%',
        disabled: false
    },
    {
        icon: DefenseImage,
        title: 'Defense +',
        text: ['Boost your Defense ', <b>+1</b>, ' point'],
        subText: ['Chance to start fighting', 'with an Attack Buff'],
        level1: '2%',
        level2: '10%',
        level3: '25%',
        disabled: false
    },
    {
        icon: StrenghtImage,
        title: 'Strenght',
        text: ['Boost your Attack Range'],
        subText: ['Each level represente the number of points added to your range'],
        level1: '1',
        level2: '2',
        level3: '4',
        disabled: true
    },
    {
        icon: ResistanceImage,
        title: 'Resistance',
        text: ['Boost your Defense Range'],
        subText: ['Each level represente the number of points added to your range'],
        level1: '1',
        level2: '2',
        level3: '4',
        disabled: false
    },
    {
        icon: UppercutImage,
        title: 'Uppercut',
        text: ['Boost your Attack ', <b>+10</b>, ' point'],
        subText: ['Chance to trigger during the fight'],
        level1: '1%',
        level2: '3%',
        level3: '5%',
        disabled: false
    },
    {
        icon: DodgeImage,
        title: 'Dodge',
        text: ['Dodge the opponent Attack'],
        subText: ['Chance to trigger during the fight'],
        level1: '1%',
        level2: '3%',
        level3: '5%',
        disabled: false
    },
    {
        icon: PoisonedClawsImage,
        title: 'Poisoned Claws',
        text: ['Attack ', <b>+2</b>, ' each round'],
        subText: ['Chance to trigger for the fight'],
        level1: '2%',
        level2: '5%',
        level3: '10%',
        disabled: false
    },
    {
        icon: BandagesImage,
        title: 'Bandages',
        text: ['Defense ', <b>+2</b>, ' each round'],
        subText: ['Chance to trigger for the fight'],
        level1: '2%',
        level2: '5%',
        level3: '10%',
        disabled: false
    },
]

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const SkillsModal = ({open, onClose}) => {
    const [activeIndex, setActiveIndex] = useState(0);
    return (
        <Dialog 
            TransitionComponent={Transition}
            onClose={onClose} 
            open={open} 
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 700,
                    width: '100%'
                }
            }}
        >
            <Paper>
                <Box sx={{
                    p: '8px 24px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between'
                }}>
                    <Typography sx={{
                        fontSize: 15,
                        fontFamily: 'Poppins-Medium',
                        lineHeight: '22px',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}>
                        Attributes
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        alignItems: 'center'
                    }}>
                        <img src={AttackImage} alt="" height={15} />
                        <Typography sx={{
                            fontSize: 16,
                            fontFamily: 'Poppins-Bold',
                            lineHeight: '18px',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            ml: '4px'
                        }}>
                            4
                        </Typography>
                        <Typography sx={{
                            fontSize: 12,
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '18px',
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            mx: '10px'
                        }}>
                            +
                        </Typography>
                        <img src={DefenseImage} alt="" height={15} />
                        <Typography sx={{
                            fontSize: 16,
                            fontFamily: 'Poppins-Bold',
                            lineHeight: '18px',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            ml: '4px'
                        }}>
                            3
                        </Typography>
                        <Typography sx={{
                            fontSize: 12,
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '18px',
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            mx: '10px'
                        }}>
                            =
                        </Typography>
                        <Typography sx={{
                            fontSize: 16,
                            fontFamily: 'Poppins-Bold',
                            lineHeight: '18px',
                            letterSpacing: '0.01em',
                            color: '#CD1322',
                            mr: '4px'
                        }}>
                            7
                        </Typography>
                        <Typography sx={{
                            fontSize: 12,
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '18px',
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                        }}>
                            Skills Points
                        </Typography>
                    </Box>
                    <IconButton size="small" onClick={onClose}>
                        <CloseIcon />
                    </IconButton>
                </Box>
                <Divider />
                <Box sx={{
                    p: '42px 24px 24px'
                }}>
                    <Typography sx={{
                        fontSize: 15,
                        fontFamily: 'Poppins-Bold',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                        mb: '5px'
                    }}>
                        Choose your Attributes
                    </Typography>    
                    <Typography sx={{
                        fontSize: 14,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '21px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        mb: '24px'
                    }}>
                        Each Rooster can unlock and choose specialized skills.
                        <br />
                        Improve your Attack and Defense level to unlock them.
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        flexDirection: { xs: 'column', sm: 'row' }
                    }}>
                        <Box sx={{
                            flexGrow: '1',
                            mb: { xs: '24px', sm: '0' }
                        }}>
                            <Attributes activeIndex={activeIndex} setActiveIndex={setActiveIndex} />
                        </Box>
                        <Box sx={{
                            pl: { xs: '0', sm: '24px' },
                            borderLeft: { xs: '0', sm: '1px solid #EBEFF2' },
                            ml: { xs: '0', sm: '24px' }
                        }}>
                            <Box sx={{
                                minWidth: { xs: '100%', sm: 200 },
                                width: { xs: '100%', sm: 200 },
                            }}>
                                <Box sx={{
                                    width: '50px',
                                    minWidth: '50px',
                                    height: '50px',
                                    mx: 'auto',
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    border: '1px solid #192A3E',
                                    borderRadius: '50%',
                                    mb: '10px',
                                    userSelect: 'none'
                                }}>
                                    <img src={skillsLists[activeIndex].icon} alt="" width={22} />
                                </Box>
                                <Typography sx={{
                                    fontSize: 15,
                                    fontFamily: 'Poppins-Bold',
                                    lineHeight: '22px',
                                    textAlign: 'center',
                                    letterSpacing: '0.01em',
                                    color: '#192A3E',
                                    mb: '8px'
                                }}>
                                    {skillsLists[activeIndex].title}
                                </Typography>
                                <Box sx={{
                                    display: 'flex',
                                    justifyContent: 'center',
                                    mb: '16px'
                                }}>
                                    <Box component="img" src={GoldStar} alt="" />
                                    <Box component="img" src={WhiteStar} alt="" sx={{ mx: "8px" }} />
                                    <Box component="img" src={WhiteStar} alt="" />
                                </Box>
                                <Typography sx={{
                                    fontSize: 14,
                                    fontFamily: 'Poppins-Regular',
                                    lineHeight: '24px',
                                    textAlign: 'center',
                                    letterSpacing: '0.01em',
                                    color: '#192A3E',
                                    mb: '10px'
                                }}>
                                    {skillsLists[activeIndex].text.map((text, key) => (
                                        <span key={key}>{text}</span>
                                    ))}
                                </Typography>
                                {skillsLists[activeIndex].subText.map((text, key) => (
                                    <Typography key={key} sx={{
                                        fontSize: 10,
                                        fontFamily: 'Poppins-Regular',
                                        lineHeight: '15px',
                                        textAlign: 'center',
                                        letterSpacing: '0.01em',
                                        color: '#A6ACB1',
                                    }}>
                                        {text}
                                    </Typography>
                                ))}
                                <Box sx={{
                                    display: 'flex',
                                    justifyContent: 'space-around',
                                    mt: '12px',
                                    mb: '24px'
                                }}>
                                    <Box>
                                        <Typography sx={{
                                            fontSize: 12,
                                            fontFamily: 'Poppins-Regular',
                                            lineHeight: '18px',
                                            textAlign: 'center',
                                            letterSpacing: '0.01em',
                                            color: '#4C5862'
                                        }}>
                                            Level 1
                                        </Typography>
                                        <Typography sx={{
                                            fontSize: 12,
                                            fontFamily: 'Poppins-Bold',
                                            lineHeight: '18px',
                                            textAlign: 'center',
                                            letterSpacing: '0.01em',
                                            color: '#4C5862'
                                        }}>
                                            {skillsLists[activeIndex].level1}
                                        </Typography>
                                    </Box>
                                    <Box>
                                        <Typography sx={{
                                            fontSize: 12,
                                            fontFamily: 'Poppins-Regular',
                                            lineHeight: '18px',
                                            textAlign: 'center',
                                            letterSpacing: '0.01em',
                                            color: '#4C5862'
                                        }}>
                                            Level 2
                                        </Typography>
                                        <Typography sx={{
                                            fontSize: 12,
                                            fontFamily: 'Poppins-Bold',
                                            lineHeight: '18px',
                                            textAlign: 'center',
                                            letterSpacing: '0.01em',
                                            color: '#4C5862'
                                        }}>
                                            {skillsLists[activeIndex].level2}
                                        </Typography>
                                    </Box>
                                    <Box>
                                        <Typography sx={{
                                            fontSize: 12,
                                            fontFamily: 'Poppins-Regular',
                                            lineHeight: '18px',
                                            textAlign: 'center',
                                            letterSpacing: '0.01em',
                                            color: '#4C5862'
                                        }}>
                                            Level 3
                                        </Typography>
                                        <Typography sx={{
                                            fontSize: 12,
                                            fontFamily: 'Poppins-Bold',
                                            lineHeight: '18px',
                                            textAlign: 'center',
                                            letterSpacing: '0.01em',
                                            color: '#4C5862'
                                        }}>
                                            {skillsLists[activeIndex].level3}
                                        </Typography>
                                    </Box>
                                </Box>
                                {skillsLists[activeIndex].disabled ? (
                                    <Button
                                        fullWidth
                                        variant='contained'
                                        disabled
                                        onClick={onClose}
                                        sx={{
                                            background: '#C4C4C4!important',
                                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                                            fontFamily: "Poppins-SemiBold",
                                            color: '#fff!important',
                                            fontSize: '12px',
                                            letterSpacing: '0.01em',
                                            textTransform: 'capitalize',
                                            minHeight: '37px',
                                            mb: '12px'
                                        }}
                                    >
                                        <Box
                                            component="img"
                                            src={LockImage}
                                            alt=""
                                            sx={{ mr: '5px' }}
                                        />
                                        Learn this skill
                                    </Button>
                                ) : (
                                    <Button
                                        fullWidth
                                        variant='contained'
                                        onClick={onClose}
                                        sx={{
                                            background: '#BF0C1A!important',
                                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                                            fontFamily: "Poppins-SemiBold",
                                            fontSize: '12px',
                                            letterSpacing: '0.01em',
                                            textTransform: 'capitalize',
                                            minHeight: '37px',
                                        }}
                                    >
                                        Unlock Level 2
                                        <Typography
                                            component="span"
                                            sx={{
                                                fontSize: 10,
                                                fontFamily: 'Poppins-Regular',
                                                letterSpacing: '0.01em',
                                                ml: '4px'
                                            }}
                                        >
                                            (1000 CORN$)
                                        </Typography>
                                    </Button>
                                )}
                                {skillsLists[activeIndex].disabled && <Box sx={{
                                    borderLeft: '5px red solid',
                                    height: '87px',
                                    borderRadius: '4px',
                                    boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                }}>
                                    <Box
                                        component="img"
                                        src={InfoImage}
                                        alt=""
                                        width={20}
                                    />
                                    <Typography sx={{
                                        fontSize: 10,
                                        fontFamily: 'Poppins-Regular',
                                        fontStyle: 'italic',
                                        lineHeight: '15px',
                                        letterSpacing: '0.01em',
                                        color: '#A6ACB1',
                                        width: 113,
                                        ml: '20px',
                                    }}>
                                        Unlock your fourth skill then reset your specializations tree to unlock this skill.
                                    </Typography>
                                </Box>}
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Paper>
        </Dialog>
    );
}

export default SkillsModal;