import React, { Fragment, useState } from 'react';
import { 
    Divider,
    Box,
    Menu,
    MenuItem,
    Button
} from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

const lists = [
    {
        id: 'Tournament #1851851'
    },
    {
        id: 'Tournament #1851850'
    },
    {
        id: 'Tournament #1851849'
    },
]

const Brackets = () => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [selectId, setSelectId] = useState(0);
    const handleSelect = (index) => {
        setSelectId(index);
        setAnchorEl(null);
    };
    return (
        <Fragment>
            <Button
                id="basic-button"
                aria-controls={Boolean(anchorEl) ? 'basic-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={Boolean(anchorEl) ? 'true' : undefined}
                onClick={(e) => setAnchorEl(e.currentTarget)}
                sx={{
                    fontSize: '15px',
                    fontFamily: 'Poppins-Medium',
                    lineHeight: '22px',
                    letterSpacing: '0.01em',
                    color: '#192A3E',
                    textTransform: 'capitalize',
                    m: '12px 24px 8px',
                }}
            >
                {lists[selectId].id}
                <ArrowDropDownIcon fontSize="small" sx={{ color: '#C4C4C4' }} />
            </Button>
            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={() => setAnchorEl(null)}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                MenuListProps={{
                'aria-labelledby': 'basic-button',
                }}
            >
                {lists.map((item, key) => (
                    <MenuItem key={key} onClick={() => handleSelect(key)}>
                        {item.id}
                    </MenuItem>
                ))}
            </Menu>
            <Divider />
            <Box
                sx={{
                    p: '24px',
                    height: 1145
                }}
            >
            </Box>
        </Fragment>
    )
}

export default Brackets;