const useStyles = makeStyles(theme => ({
    sliderItemText: {
        display: 'flex',
        alignItems: 'center',
        fontFamily: 'Poppins-Bold',
        fontSize: '14px',
        lineHeight: '18px',
        letterSpacing: '0.01em',
        color: '#4C5862',
        "& img": {
            marginRight: '4px'
        }
    },
}));

const FightResult = ({date, firstScore, secondScore, onClick}) => {
    return (
        <Box sx={{
            borderBottom: '1px solid #EBEFF2',
            p: '12px 10px',
            height: '77px',
            display: 'flex',
        }}>
            
            <Box sx={{
                borderRight: '1px solid #EBEFF2',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexBasis: '40%',
            }}>
                <Typography sx={{
                    fontSize: '10px',
                    fontFamily: 'Poppins-Regular',
                    letterSpacing: '0.01em',
                    color: '#4C5862',
                    opacity: '0.5',
                    mr: '18px'
                }}>
                    {date}
                </Typography>
                <Box>
                    <Typography sx={{
                        fontSize: '15px',
                        fontFamily: 'Poppins-Bold',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: firstScore > secondScore ? '#33BC84' : '#192A3E',
                    }}>
                        {firstScore}
                    </Typography>
                    <Typography sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '15px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5',
                    }}>
                        Score
                    </Typography>
                </Box>
            </Box>
            <Box sx={{
                flexGrow: '1',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                px: '10px'
            }}>
                <Typography sx={{
                    fontSize: '15px',
                    fontFamily: 'Poppins-Bold',
                    lineHeight: '22px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: firstScore > secondScore ? '#33BC84' : '#CD1322',
                }}>
                    {firstScore > secondScore ? 'WINNER' : 'LOOSER' }
                </Typography>
                <Typography sx={{
                    fontSize: '10px',
                    fontFamily: 'Poppins-Regular',
                    lineHeight: '15px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: '#4C5862',
                    opacity: '0.5',
                }}>
                    First Round
                </Typography>
            </Box>
            <Box sx={{
                borderLeft: '1px solid #EBEFF2',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexBasis: '40%',
            }}>
                <Box>
                    <Typography sx={{
                        fontSize: '15px',
                        fontFamily: 'Poppins-Bold',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: firstScore > secondScore ? '#192A3E' : '#CD1322',
                    }}>
                        {secondScore}
                    </Typography>
                    <Typography sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '15px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5',
                    }}>
                        Score
                    </Typography>
                </Box>
                <IconButton sx={{ ml: '48px' }} onClick={onClick}>
                    <img src={EyeIcon} alt="" />
                </IconButton>
            </Box>
        </Box>
    )
}

const options = {
    loop: true,
    center: true,
    autoplay: false,
    dots: false,
    merge: true,
    items: 3,
};

const Battles = () => {
    const classes = useStyles();
    const carousel = useRef(null);
    const [fightingOpen, setFightingOpen] = useState(false);
    const [scoreIndex, setScoreIndex] = useState(0);
    const [battleModalOpen, setBattleModalOpen] = useState(scoreLists[scoreIndex].map(() => false));

    const handleBattleModalOpen = (index) => {
        setBattleModalOpen(battleModalOpen => 
            [...battleModalOpen.slice(0, index).map(() => false), true, ...battleModalOpen.slice(index + 1, battleModalOpen.length).map(() => false)]
        )
    }
    $( document).ready(function() {
        $(".owl-item").click(function(){
            $(".owl-item").map((key, item) => {
                if ( item === $(this)[0]) {
                    carousel.current.to((key - 1) % 3, 0)
                }
                return null;
            })
        });
    });
    
    return (
        <Fragment>
            <WaitingModal open={fightingOpen} onClose={() => setFightingOpen(false)} />
            {scoreLists[scoreIndex].map((item, key) => (
                <BattleResultModal
                    key={key}
                    index={key}
                    open={battleModalOpen[key]}
                    onClose={() => setBattleModalOpen(battleModalOpen => battleModalOpen.map(() => false))}
                    onPrev={() => handleBattleModalOpen(key - 1)}
                    onNext={() => handleBattleModalOpen(key + 1)}
                    length={battleModalOpen.length}
                    results={item}
                />
            ))}
            <OwlCarousel className="owl-theme" {...options}>
                <div className="item"></div>
            </OwlCarousel>
            <Box sx={{
                position: 'relative',
                overflow: 'hidden',
                backgroundColor: '#fff',
                boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
                borderRadius: '8px',
                marginBottom: '24px'
            }}>
                <Typography sx={{
                    fontFamily: 'Poppins-Medium',
                    fontSize: '15px',
                    lineHeight: '22px',
                    letterSpacing: '0.01em',
                    color: '#192A3E',
                    padding: '16px 24px',
                }}>
                    Battles
                </Typography>
                <Divider />
                <Box
                    sx={{
                        m: '24px',
                        backgroundColor: '#64EC4E',
                        height: '150px',
                        borderRadius: '4px'
                    }}
                ></Box>
                <Box sx={{
                    width: '120%',
                    transform: 'translateX(-10%)',
                    "& .owl-item": {
                        cursor: 'pointer',
                        marginTop: '8px',
                        marginBottom: '8px',
                    },
                    "& .owl-item > div": {
                        transform: 'scale(0.8, 0.8)!important'
                    },
                    "& .owl-item.center > div": {
                        transform: 'scale(1, 1)!important',
                        transition: 'transform 0.3s'
                    }
                }}>
                    <OwlCarousel
                        ref={carousel}
                        className='owl-carousel owl-theme owl-loaded owl-drag'
                        {...options}
                        startPosition={scoreIndex}
                        onChanged={(e) => setScoreIndex(e.item.index % 3)}
                    >
                        {battleLists.map((item, key) => (
                            <Box
                                key={key}
                                sx={{
                                    position: 'relative',
                                    boxShadow: '0px 2px 6px rgba(0, 0, 0, 0.2)',
                                    borderRadius: '4px',
                                    pt: '16px',
                                    pb: '24px',
                                    cursor: 'pointer',
                                    display: 'block!important',
                                    mx: 'auto',
                                }}
                            >
                                <Box sx={{
                                    position: 'absolute',
                                    top: '-2px',
                                    right: '-2px',
                                    width: '92px',
                                    height: '92px',
                                    overflow: 'hidden',
                                    "&:before": {
                                        content: "''",
                                        backgroundColor: item.queue ? "#044258" : null,
                                        position: 'absolute',
                                        top: '0px',
                                        right: '47px',
                                        width: '40px',
                                        height: '2px',
                                        borderRadius: '2px 0 0 0',

                                    },
                                    "&:after": {
                                        content: "''",
                                        backgroundColor: item.queue ? "#044258" : null,
                                        position: 'absolute',
                                        top: '46px',
                                        right: '1px',
                                        width: '40px',
                                        height: '2px',
                                        transformOrigin: 'right',
                                        transform: 'rotate(-90deg)',
                                        borderRadius: '0 0px 0 2px',
                                    }
                                }}>
                                    <Box sx={{
                                            position: 'absolute',
                                            display: 'flex',
                                            justifyContent: 'center',
                                            alignItems: 'center',
                                            fontFamily: 'Poppins-Medium',
                                            background: item.queue ? "#044258" : null,
                                            right: '-49px',
                                            top: '15px',
                                            fontSize: '11px',
                                            color: '#fff',
                                            transform: 'rotate(45deg)',
                                            width: '160px',
                                            height: '32px',
                                    }}>
                                        {item.queue ? "In queue" : null}
                                    </Box>
                                </Box>
                                <Box sx={{
                                    display: 'flex',
                                    justifyContent: 'space-between',
                                    alignItems: 'center',
                                    mb: '14px',
                                    px: '8px'
                                }}>
                                    <Typography sx={{
                                        fontSize: '15px',
                                        letterSpacing: '0.01em',
                                        color: '#323C47',
                                        fontFamily: 'Poppins-Medium'
                                    }}>
                                        #00001
                                    </Typography>
                                    <Box sx={{
                                        display: 'flex',
                                        alignItems: 'center'
                                    }}>
                                        <Typography sx={{
                                            fontSize: '14px',
                                            letterSpacing: '0.01em',
                                            color: '#4C5862',
                                            fontFamily: 'Poppins-Bold',
                                            mr: '4px'
                                        }}>
                                            3
                                        </Typography>
                                        <img src={Diamond} alt="" width={16} />
                                    </Box>
                                </Box>
                                <Box sx={{
                                    backgroundColor: '#64EC4E',
                                    aspectRatio: '1',
                                    mb: '24px'
                                }}></Box>
                                <Box sx={{
                                    display: 'flex',
                                    justifyContent: 'space-between',
                                    alignItems: 'center',
                                    px: '12px'
                                }}>
                                    <div className={classes.sliderItemText}>
                                        <img src={AttackImage} alt="" height={15} />
                                        <span>4</span>
                                    </div>
                                    <div className={classes.sliderItemText}>
                                        <img src={HunterImage} alt="" height={15} />
                                        <span>7/<span style={{ fontFamily: 'Poppins-Regular' }}>10</span></span>
                                    </div>
                                    <div className={classes.sliderItemText}>
                                        <img src={Defense} alt="" height={15} />
                                        <span>3</span>
                                    </div>
                                </Box>
                            </Box>
                        ))}
                    </OwlCarousel>
                </Box>
                <Box sx={{
                    p: '24px 24px 0'
                }}>
                    {scoreLists[scoreIndex].map((list, key) => (
                        <FightResult key={key} date={list.date} firstScore={list.firstScore} secondScore={list.secondScore} onClick={() => handleBattleModalOpen(key)} />
                    ))}
                </Box>
                <Box sx={{ padding: '24px' }}>
                    <Button
                        variant="contained"
                        fullWidth
                        sx={{
                            background: '#BF0C1A!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                        }}
                        onClick={() => setFightingOpen(true)}
                    >
                        FIGHT !
                        <Typography
                            component="span"
                            sx={{
                                fontSize: '10px',
                                marginLeft: '4px',
                                fontFamily: 'Poppins-Regular'
                            }}
                        >
                            (10 MATIC)
                        </Typography>
                    </Button>
                </Box>
            </Box>
        </Fragment>
    )
}

export default Battles;