import React, { forwardRef, useState } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Slide,
    Divider,
    Box,
    Table,
    TableHead,
    TableBody,
    TableRow,
    TableCell,
} from '@mui/material';
import AttackImage from '../assets/images/attack.svg';
import Defense from '../assets/images/defense.svg';

const useStyles = makeStyles(theme => ({
    tableCell: {
        width: '33.33%',
        padding: '12px 2px!important',
        height: '77px',
        borderBottomColor: '#EBEFF2!important',
        "& > .MuiBox-root": {
            height: '100%',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            flexDirection: 'column',
        }
    }
}));

// const LeftTransition = forwardRef(function Transition(props, ref) {
//     return <Slide direction="left" ref={ref} {...props} />;
// });

const RightTransition = forwardRef(function Transition(props, ref) {
    return <Slide direction="right" ref={ref} {...props} />;
});

const BattleResult = ({ index, open, onClose, results, onPrev, onNext, length }) => {
    const classes = useStyles();
    const [isWinner] = useState((results.firstScore - results.secondScore) > 0)
    return (
        <Dialog 
            TransitionComponent={RightTransition}
            onClose={onClose} 
            open={open} 
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 942,
                    width: '100%'
                }
            }}
        >
            <Paper>
                <Typography sx={{
                    fontSize: 15,
                    fontFamily: 'Poppins-Medium',
                    lineHeight: '22px',
                    letterSpacing: '0.01em',
                    color: '#192A3E',
                    textAlign: 'center',
                    m: '9px 24px'
                }}>
                    Battle #35544
                </Typography>
                <Divider />
                <Box sx={{ p: '24px' }}>
                    <Box sx={{
                        backgroundColor: '#64EC4E',
                        aspectRatio: '894 / 460',
                        mb: '24px'
                    }}
                    ></Box>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'flex-start',
                        mb: '24px'
                    }}>
                        <Box sx={{
                            width: 'calc(33.33% - 16px)',
                            boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                            borderRadius: '4px',
                        }}>
                            <Typography sx={{
                                fontSize: 15,
                                lineHeight: '22px',
                                fontFamily: 'Poppins-Medium',
                                textAlign: 'center',
                                letterSpacing: '0.01em',
                                color: '#323C47',
                                mt: '14px',
                                mb: '11px'
                            }}>
                                #00001
                            </Typography>
                            <Box sx={{
                                aspectRatio: '1',
                                backgroundColor: '#64EC4E'
                            }}></Box>
                            <Box sx={{
                                p: '24px 12px',
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center'
                            }}>
                                <Box sx={{
                                    display: 'flex',
                                    alignItems: 'center'
                                }}>
                                    <img src={AttackImage} alt="" />
                                    <Typography sx={{
                                        fontSize: 14,
                                        lineHeight: '21px',
                                        letterSpacing: '0.01em',
                                        color: '#4C5862',
                                        ml: '8px'
                                    }}>
                                        Level <b>4 </b>
                                        (10-<span style={{ color: '#33BC84' }}>18</span>)
                                    </Typography>
                                </Box>
                                <Box sx={{
                                    display: 'flex',
                                    alignItems: 'center'
                                }}>
                                    <img src={Defense} alt="" />
                                    <Typography sx={{
                                        fontSize: 14,
                                        lineHeight: '21px',
                                        letterSpacing: '0.01em',
                                        color: '#4C5862',
                                        ml: '8px'
                                    }}>
                                        Level <b>3 </b>
                                        (8-<span style={{ color: '#33BC84' }}>12</span>)
                                    </Typography>
                                </Box>
                            </Box>
                        </Box>
                        <Box sx={{
                            width: 'calc(33.33% - 16px)',
                            boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                            borderRadius: '4px',
                            mt: '48px'
                        }}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell className={classes.tableCell}>
                                            <Box sx={{ borderRight: '1px solid #EBEFF2' }}>
                                                <Typography sx={{
                                                    fontSize: 15,
                                                    fontFamily: 'Poppins-Bold',
                                                    lineHeight: '22px',
                                                    textAlign: 'center',
                                                    letterSpacing: '0.01em',
                                                    color: '#33BC84',
                                                }}>
                                                    {results.firstScore}
                                                </Typography>
                                                <Typography sx={{
                                                    fontSize: 10,
                                                    lineHeight: '15px',
                                                    textAlign: 'center',
                                                    letterSpacing: '0.01em',
                                                    color: '#4C5862',
                                                    opacity: '0.5',
                                                }}>
                                                    Score
                                                </Typography>
                                            </Box>
                                        </TableCell>
                                        <TableCell className={classes.tableCell}>
                                            <Box sx={{ borderRight: '1px solid #EBEFF2' }}>
                                                <Typography sx={{
                                                    fontSize: 15,
                                                    fontFamily: 'Poppins-Bold',
                                                    lineHeight: '22px',
                                                    textAlign: 'center',
                                                    letterSpacing: '0.01em',
                                                    color: isWinner ? '#33BC84' : '#CD1322',
                                                }}>
                                                    {isWinner ? 'WINNER' : 'LOOSER'}
                                                </Typography>
                                                <Typography sx={{
                                                    fontSize: 10,
                                                    lineHeight: '15px',
                                                    textAlign: 'center',
                                                    letterSpacing: '0.01em',
                                                    color: '#4C5862',
                                                    opacity: '0.5',
                                                }}>
                                                    {results.round}
                                                </Typography>
                                            </Box>
                                        </TableCell>
                                        <TableCell className={classes.tableCell}>
                                            <Box>
                                                <Typography sx={{
                                                    fontSize: 15,
                                                    fontFamily: 'Poppins-Bold',
                                                    lineHeight: '22px',
                                                    textAlign: 'center',
                                                    letterSpacing: '0.01em',
                                                    color: '#CD1322',
                                                }}>
                                                    {results.secondScore}
                                                </Typography>
                                                <Typography sx={{
                                                    fontSize: 10,
                                                    lineHeight: '15px',
                                                    textAlign: 'center',
                                                    letterSpacing: '0.01em',
                                                    color: '#4C5862',
                                                    opacity: '0.5',
                                                }}>
                                                    Score
                                                </Typography>
                                            </Box>
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {results.scoreResults.map((result, key) => (
                                        <TableRow key={key}>
                                            <TableCell className={classes.tableCell}>
                                                <Box sx={{ borderRight: '1px solid #EBEFF2' }}>
                                                    <Box sx={{
                                                        display: 'flex',
                                                        alignItems: 'center'
                                                    }}>
                                                        <img src={AttackImage} alt="" width={15} />
                                                        <Typography sx={{
                                                            fontSize: 16,
                                                            fontFamily: 'Poppins-Regular',
                                                            lineHeight: '24px',
                                                            letterSpacing: '0.01em',
                                                            color: '#4C5862',
                                                            ml: '7px'
                                                        }}>
                                                            {result.firstAttack}
                                                        </Typography>
                                                    </Box>
                                                    <Box sx={{
                                                        display: 'flex',
                                                        alignItems: 'center'
                                                    }}>
                                                        <img src={Defense} alt="" width={15} />
                                                        <Typography sx={{
                                                            fontSize: 16,
                                                            fontFamily: 'Poppins-Regular',
                                                            lineHeight: '24px',
                                                            letterSpacing: '0.01em',
                                                            color: '#4C5862',
                                                            ml: '7px'
                                                        }}>
                                                            {result.firstDefense}
                                                        </Typography>
                                                    </Box>
                                                </Box>
                                            </TableCell>
                                            <TableCell className={classes.tableCell}>
                                                <Box sx={{ borderRight: '1px solid #EBEFF2' }}>
                                                    <Typography sx={{
                                                        fontSize: 16,
                                                        fontFamily: 'Poppins-Bold',
                                                        lineHeight: '24px',
                                                        letterSpacing: '0.01em',
                                                        color: isWinner ? 
                                                            (result.firstAttack - result.secondDefense > 0 ? '#33BC84' : '#192A3E') : 
                                                            (result.firstAttack - result.secondDefense < 0 ? '#CD1322' : '#192A3E'),
                                                        ml: '7px'
                                                    }}>
                                                        {Math.abs(result.firstAttack - result.secondDefense)}
                                                    </Typography>
                                                    <Typography sx={{
                                                        fontSize: 16,
                                                        fontFamily: 'Poppins-Bold',
                                                        lineHeight: '24px',
                                                        letterSpacing: '0.01em',
                                                        color: isWinner ? 
                                                            (result.firstDefense - result.secondAttack > 0 ? '#33BC84' : '#192A3E') : 
                                                            (result.firstDefense - result.secondAttack < 0 ? '#CD1322' : '#192A3E'),
                                                        ml: '7px'
                                                    }}>
                                                        {Math.abs(result.firstDefense - result.secondAttack)}
                                                    </Typography>
                                                </Box>
                                            </TableCell>
                                            <TableCell className={classes.tableCell}>
                                                <Box>
                                                    <Box sx={{
                                                            display: 'flex',
                                                            alignItems: 'center'
                                                        }}>
                                                            <img src={Defense} alt="" width={15} />
                                                            <Typography sx={{
                                                                fontSize: 16,
                                                                fontFamily: 'Poppins-Regular',
                                                                lineHeight: '24px',
                                                                letterSpacing: '0.01em',
                                                                color: '#4C5862',
                                                                ml: '7px'
                                                            }}>
                                                                {result.secondDefense}
                                                            </Typography>
                                                        </Box>
                                                        <Box sx={{
                                                            display: 'flex',
                                                            alignItems: 'center'
                                                        }}>
                                                            <img src={AttackImage} alt="" width={15} />
                                                            <Typography sx={{
                                                                fontSize: 16,
                                                                fontFamily: 'Poppins-Regular',
                                                                lineHeight: '24px',
                                                                letterSpacing: '0.01em',
                                                                color: '#4C5862',
                                                                ml: '7px'
                                                            }}>
                                                                {result.secondAttack}
                                                            </Typography>
                                                        </Box>
                                                </Box>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </Box>
                        <Box sx={{
                            width: 'calc(33.33% - 16px)',
                            boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                            borderRadius: '4px',
                        }}>
                            <Typography sx={{
                                fontSize: 15,
                                lineHeight: '22px',
                                fontFamily: 'Poppins-Medium',
                                textAlign: 'center',
                                letterSpacing: '0.01em',
                                color: '#323C47',
                                mt: '14px',
                                mb: '11px'
                            }}>
                                #00002
                            </Typography>
                            <Box sx={{
                                aspectRatio: '1',
                                backgroundColor: '#64EC4E'
                            }}></Box>
                            <Box sx={{
                                p: '24px 12px',
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center'
                            }}>
                                <Box sx={{
                                    display: 'flex',
                                    alignItems: 'center'
                                }}>
                                    <img src={AttackImage} alt="" />
                                    <Typography sx={{
                                        fontSize: 14,
                                        lineHeight: '21px',
                                        letterSpacing: '0.01em',
                                        color: '#4C5862',
                                        ml: '8px'
                                    }}>
                                        Level <b>4 </b>
                                        (10-<span style={{ color: '#CD1322' }}>18</span>)
                                    </Typography>
                                </Box>
                                <Box sx={{
                                    display: 'flex',
                                    alignItems: 'center'
                                }}>
                                    <img src={Defense} alt="" />
                                    <Typography sx={{
                                        fontSize: 14,
                                        lineHeight: '21px',
                                        letterSpacing: '0.01em',
                                        color: '#4C5862',
                                        ml: '8px'
                                    }}>
                                        Level <b>3 </b>
                                        (8-<span style={{ color: '#CD1322' }}>12</span>)
                                    </Typography>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                    <Box sx={{
                        display: 'flex',
                        flexDirection: { xs: 'column', sm: 'row' }
                    }}>
                        {index !== 0 && <Button 
                            variant='contained'
                            onClick={onPrev}
                            sx={{
                                flexBasis: '50%',
                                color: '#fff!important',
                                background: '#CD1322!important',
                                boxShadow: '0px 2px 10px rgba(4, 66, 88, 0.08)',
                                fontFamily: "Poppins-SemiBold",
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                minHeight: '37px',
                                mb: { xs: '16px' , sm: 0 },
                                mr: { sm: '24px' },
                            }}
                        >
                            Previous Round
                        </Button>}
                        {(index === 0 || index === (length - 1)) && <Button
                            variant='outlined'
                            onClick={onClose}
                            sx={{
                                flexBasis: '50%',
                                color: '#CD1322',
                                fontFamily: "Poppins-SemiBold",
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                borderColor: '#CD1322',
                                minHeight: '37px',
                                mb: { xs: index === 0 ? '16px': 0, sm: 0 },
                                mr: { sm: index === 0 ? '24px': 0 },
                                "&:hover": {
                                    borderColor: '#CD1322',
                                }
                            }}
                        >
                            Close
                        </Button>}
                        {index !== (length - 1) && <Button 
                            variant='contained'
                            onClick={onNext}
                            sx={{
                                flexBasis: '50%',
                                color: '#fff!important',
                                background: '#CD1322!important',
                                boxShadow: '0px 2px 10px rgba(4, 66, 88, 0.08)',
                                fontFamily: "Poppins-SemiBold",
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                minHeight: '37px',
                            }}
                        >
                            Next Round
                        </Button>}
                    </Box>
                </Box>
            </Paper>
        </Dialog>
    );
}

export default BattleResult;