import React, { useState, forwardRef } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Box,
    LinearProgress,
    Slide
} from '@mui/material';
import eggImage from '../assets/images/illu_egg.png';
import EggIcon from '../assets/images/egg.svg';

const useStyles = makeStyles(theme => ({
    button: {
        background: '#FEB12F!important',
        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)!important',
        fontFamily: 'Poppins-SemiBold!important',
        fontSize: '12px!important',
        letterSpacing: '0.01em!important',
        textTransform: 'capitalize!important',
        minHeight: '37px',
    },
    pelfImage: {
        display: 'block',
        margin: '20px auto 20px'
    },
    borderLinearProgress: {
        height: '6px!important',
        borderRadius: 3,
        margin: '6px 0',
        backgroundColor: '#EBEFF2!important',
        "& > span": {
            borderRadius: 3
        }
    },
    input: {
        color: '#FEB12F',
        fontFamily: 'Poppins-SemiBold',
        fontSize: '12px',
        letterSpacing: '0.01em',
        textTransform: 'capitalize',
        border: '1px solid #FEB12F',
        borderRadius: '4px',
        outline: 'none',
        textAlign: 'center',
        marginRight: '12px',
        width: '30%',
    }
}));

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const ImproveModal = ({open, onClose}) => {
    const classes = useStyles();
    const [number, setNumber] = useState(0);
    return (
        <Dialog 
            onClose={onClose} 
            open={open} 
            TransitionComponent={Transition}
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper sx={{
                padding: '46px 24px 24px'
            }}>
                <Typography
                    sx={{
                        fontFamily: 'Poppins-Bold',
                        fontSize: '15px',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}
                >
                    Improve your Egg Rarity
                </Typography>
                <Typography
                    sx={{
                        fontSize: '12px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}
                >
                    Use your SOY to improve your egg rarity<br />
                    and get more chance for better openings.
                </Typography>
                <img className={classes.pelfImage} src={eggImage} alt="" />
                <Box sx={{marginBottom: '24px'}}>
                    <Box sx={{
                        display: 'flex',
                        alignItems: 'center'
                    }}>
                        <img src={EggIcon} alt="" />
                        <Typography
                            sx={{
                                fontSize: '12px',
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '18px',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                margin: '0 12px 0 8px'
                            }}
                        >
                            Egg Rarity : 
                        </Typography>
                        <Typography
                            sx={{
                                fontFamily: 'Poppins-Bold',
                                fontSize: '14px',
                                lineHeight: '21px',
                                display: 'flex',
                                alignItems: 'flex-end',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                flexGrow: 1
                            }}
                        >
                            Common
                        </Typography>
                        <Typography
                            sx={{
                                fontSize: '10px',
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '15px',
                                textAlign: 'right',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                opacity: '0.5'
                            }}
                        >
                            (1,000 / 40,000 SOY)
                        </Typography>
                    </Box>
                    <LinearProgress 
                        className={classes.borderLinearProgress} 
                        variant="determinate" 
                        value={10} 
                        sx={{
                            "& > span": {
                                backgroundColor: '#FEB12F'
                            }
                        }}
                    />
                </Box>
                <Box sx={{
                    display: 'flex',
                    marginBottom: '12px'
                }}>
                    <input
                        value={number}
                        onChange={(e) => setNumber(e.target.value)}
                        className={classes.input}
                    />
                    <Button 
                        variant='contained'
                        className={classes.button}
                        onClick={onClose}
                        sx={{
                            flexGrow: '1'
                        }}
                    >
                        Improve
                    </Button>
                </Box>
                
                <Button
                    variant='outlined'
                    fullWidth
                    onClick={onClose}
                    sx={{
                        color: '#CD1322',
                        fontFamily: 'Poppins-SemiBold',
                        fontSize: '12px',
                        minHeight: '37px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        borderColor: '#CD1322',
                        "&:hover": {
                            borderColor: '#CD1322',
                        }
                    }}
                >
                    Cancel
                </Button>
            </Paper>
        </Dialog>
    );
}

export default ImproveModal;