
import React, { Fragment, useState } from 'react';
import { 
    Typography,
    Divider,
    Box,
    TextField,
    IconButton,
    InputAdornment,
    Table,
    TableBody,
    TableRow,
    TableCell,
    Link,
    Button,
    Menu,
    MenuItem,
} from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import SearchIcon from '@mui/icons-material/Search';
import BoxArrowUpRight from '../../assets/images/box-arrow-up-right.svg';
import CoinIcon from '../../assets/images/coin-icon.png';
import PelfIcon from '../../assets/images/pelf.png';

const createData = (index, id, winnerNumber, losterNumber, coin, pelf) => {
    return { index, id, winnerNumber, losterNumber, coin, pelf };
}

