const FightResult = ({id, firstScore, secondScore, onClick}) => {
    return (
        <Box sx={{
            borderBottom: '1px solid #EBEFF2',
            p: '12px 10px',
            height: '77px',
            display: 'flex',
        }}>
            
            <Box sx={{
                borderRight: '1px solid #EBEFF2',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexBasis: '40%',
            }}>
                <Typography sx={{
                    fontSize: '10px',
                    fontFamily: 'Poppins-Regular',
                    letterSpacing: '0.01em',
                    color: '#4C5862',
                    opacity: '0.5',
                    mr: '18px'
                }}>
                    {id}
                </Typography>
                <Box>
                    <Typography sx={{
                        fontSize: '15px',
                        fontFamily: 'Poppins-Bold',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: firstScore > secondScore ? '#33BC84' : '#192A3E',
                    }}>
                        {firstScore}
                    </Typography>
                    <Typography sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '15px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5',
                    }}>
                        Score
                    </Typography>
                </Box>
            </Box>
            <Box sx={{
                flexGrow: '1',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                px: '10px'
            }}>
                <Typography sx={{
                    fontSize: '15px',
                    fontFamily: 'Poppins-Bold',
                    lineHeight: '22px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: firstScore > secondScore ? '#33BC84' : '#CD1322',
                }}>
                    {firstScore > secondScore ? 'WINNER' : 'LOOSER' }
                </Typography>
                <Typography sx={{
                    fontSize: '10px',
                    fontFamily: 'Poppins-Regular',
                    lineHeight: '15px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: '#4C5862',
                    opacity: '0.5',
                }}>
                    First Round
                </Typography>
            </Box>
            <Box sx={{
                borderLeft: '1px solid #EBEFF2',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexBasis: '40%',
            }}>
                <Box>
                    <Typography sx={{
                        fontSize: '15px',
                        fontFamily: 'Poppins-Bold',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: firstScore > secondScore ? '#192A3E' : '#CD1322',
                    }}>
                        {secondScore}
                    </Typography>
                    <Typography sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '15px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5',
                    }}>
                        Score
                    </Typography>
                </Box>
                <IconButton sx={{ ml: '48px' }} onClick={onClick}>
                    <img src={EyeIcon} alt="" />
                </IconButton>
            </Box>
        </Box>
    )
}

const TournamentResults = () => {
    const [scoreIndex, setScoreIndex] = useState(0);
    const [battleModalOpen, setBattleModalOpen] = useState(scoreLists[scoreIndex].map(() => false));
    const carousel = useRef(null);

    const handleBattleModalOpen = (index) => {
        setBattleModalOpen(battleModalOpen => 
            [...battleModalOpen.slice(0, index).map(() => false), true, ...battleModalOpen.slice(index + 1, battleModalOpen.length).map(() => false)]
        )
    }

    $( document).ready(function() {
        $(".owl-item").click(function(){
            $(".owl-item").map((key, item) => {
                if ( item === $(this)[0]) {
                    carousel.current.to((key - 1) % 3, 0)
                }
                return null;
            })
        });
    });
    
    return (
        <Fragment>
            {scoreLists[scoreIndex].map((item, key) => (
                <BattleResultModal
                    key={key}
                    index={key}
                    open={battleModalOpen[key]}
                    onClose={() => setBattleModalOpen(battleModalOpen => battleModalOpen.map(() => false))}
                    onPrev={() => handleBattleModalOpen(key - 1)}
                    onNext={() => handleBattleModalOpen(key + 1)}
                    length={battleModalOpen.length}
                    results={item}
                />
            ))}
            <OwlCarousel className="owl-theme" {...options}>
                <div className="item"></div>
            </OwlCarousel>
            <Box sx={{
                position: 'relative',
                overflow: 'hidden',
                backgroundColor: '#fff',
                boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
                borderRadius: '8px',
                marginBottom: '24px'
            }}>
                <Typography sx={{
                    fontFamily: 'Poppins-Medium',
                    fontSize: '15px',
                    lineHeight: '22px',
                    letterSpacing: '0.01em',
                    color: '#192A3E',
                    padding: '16px 24px',
                }}>
                    Your Tournament Results
                </Typography>
                <Divider />
                <Box
                    sx={{
                        backgroundImage: `url(${TournamentBg2})`,
                        backgroundSize: '100% auto',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        pt: '45px',
                        pb: '40px'
                    }}
                >
                    <img src={PocketImage} alt="" />
                    <Box>
                        <Typography sx={{
                            fontSize: 15,
                            lineHeight: '22px',
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#192A3E',
                            fontFamily: 'Poppins-Bold',
                            mb: '4px'
                        }}>
                            Your best rank is Top50 !
                        </Typography>
                        <Typography 
                            component="div"
                            sx={{
                                fontSize: 14,
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '21px',
                                textAlign: 'center',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                mb: '4px'
                            }}
                        >
                            You won <b>&nbsp;2,000</b>
                            <Box
                                component="img"
                                src={CoinIcon}
                                alt=""
                                width={17}
                                sx={{ ml: '4px' }}
                            />
                        </Typography>
                        <Typography 
                            component="div"
                            sx={{
                                fontSize: 14,
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '21px',
                                textAlign: 'center',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center'
                            }}
                        >
                            And <b>&nbsp;3,000</b>
                            <Box
                                component="img"
                                src={PelfIcon}
                                alt=""
                                width={34}
                                sx={{ ml: '4px' }}
                            />
                        </Typography>
                    </Box>
                </Box>
                <Box sx={{
                    width: '120%',
                    transform: 'translateX(-10%)',
                    "& .owl-item": {
                        cursor: 'pointer',
                        marginTop: '8px',
                        marginBottom: '8px'
                    },
                    "& .owl-item > div": {
                        transform: 'scale(0.8, 0.8)!important'
                    },
                    "& .owl-item.center > div": {
                        transform: 'scale(1, 1)!important',
                        transition: 'transform 0.3s'
                    }
                }}>
                    <OwlCarousel
                        className='owl-theme'
                        ref={carousel}
                        {...options}
                        startPosition={scoreIndex}
                        onChanged={(e) => setScoreIndex(e.item.index % 3)}
                    >
                        {sliderItems.map((item, key) => (
                            <Box key={key} sx={{
                                boxShadow: '0px 2px 6px rgba(0, 0, 0, 0.2)',
                                borderRadius: '4px',
                                pt: '11px',
                                pb: '10px',
                            }}>
                                <Typography sx={{
                                    letterSpacing: '0.01em',
                                    color: '#323C47',
                                    fontFamily: 'Poppins-Medium',
                                    textAlign: 'center',
                                    mb: '11px',
                                    fontSize: '15px',
                                    lineHeight: '22px',
                                }}>
                                    #00001
                                </Typography>
                                <Box sx={{
                                    backgroundColor: '#64EC4E',
                                    aspectRatio: '1',
                                    mb: '12px'
                                }}></Box>
                                <Box sx={{
                                    display: 'flex',
                                    justifyContent: 'space-between',
                                    alignItems: 'center',
                                    px: '12px'
                                }}>
                                    <Box sx={{
                                        display: 'flex',
                                        justifyContent: 'center',
                                        alignItems: 'center'
                                    }}>
                                        <Box
                                            component="img"
                                            src={WinStar}
                                            alt=""
                                            sx={{ width: '40px!important' }}
                                        />
                                        <Typography sx={{
                                            fontSize: 15,
                                            lineHeight: '22px',
                                            fontFamily: 'Poppins-Bold',
                                            letterSpacing: '0.01em',
                                            color: '#192A3E',
                                        }}>
                                            Top {item.score}
                                        </Typography>
                                    </Box>
                                </Box>
                            </Box>
                        ))}
                    </OwlCarousel>
                </Box>
                <Box sx={{
                    p: '17px 0 0'
                }}>
                    {scoreLists[scoreIndex].map((list, key) => (
                        <FightResult key={key} id={list.id} firstScore={list.firstScore} secondScore={list.secondScore} onClick={() => handleBattleModalOpen(key)} />
                    ))}
                </Box>
                <Box sx={{ padding: '24px' }}>
                    <Button
                        variant="contained"
                        fullWidth
                        sx={{
                            background: '#BF0C1A!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                        }}
                    >
                        Result
                    </Button>
                </Box>
            </Box>
        </Fragment>
    )
}

export default TournamentResults;