import React, { forwardRef } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Slide
} from '@mui/material';
import MeatImage from '../assets/images/meat.png';
import Fire from '../assets/images/fire.svg';

const useStyles = makeStyles(theme => ({
    button: {
        background: '#BF0C1A!important',
        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)!important',
        fontFamily: "Poppins-SemiBold!important",
        fontSize: '12px!important',
        letterSpacing: '0.01em!important',
        textTransform: 'capitalize!important',
        minHeight: '37px',
    },
    image: {
        marginRight: '4px',
    },
    meatImage: {
        display: 'block',
        margin: 'auto'
    }
}));

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const BurnModal = ({open, onClose}) => {
    const classes = useStyles();
    return (
        <Dialog 
            TransitionComponent={Transition}
            onClose={onClose} 
            open={open} 
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper sx={{
                padding: '46px 24px 24px'
            }}>
                <Typography
                    sx={{
                        fontFamily: "Poppins-Bold",
                        fontSize: '15px',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}
                >
                    Burn your Rooster
                </Typography>
                <Typography
                    sx={{
                        fontSize: '12px',
                        lineHeight: '18px',
                        fontFamily: "Poppins-Regular",
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        mb: '58px'
                    }}
                >
                    Get your 3.000 CORN$ back.
                </Typography>
                <img className={classes.meatImage} src={MeatImage} alt="" />
                <Button 
                    fullWidth
                    variant='contained'
                    className={classes.button}
                    onClick={onClose}
                    sx={{
                        mt: '48px',
                        mb: '12px'
                    }}
                >
                    <img className={classes.image} src={Fire} alt="" />
                    Burn
                </Button>
                <Button
                    variant='outlined'
                    fullWidth
                    onClick={onClose}
                    sx={{
                        color: '#CD1322',
                        fontFamily: "Poppins-SemiBold",
                        fontSize: '12px',
                        minHeight: '37px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        borderColor: '#CD1322',
                        "&:hover": {
                            borderColor: '#CD1322',
                        }
                    }}
                >
                    Cancel
                </Button>
            </Paper>
        </Dialog>
    );
}

export default BurnModal;