import React, { Fragment, useState } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Typography,
    Divider,
    Box,
    IconButton,
    LinearProgress,
    Button
} from '@mui/material';
import { useHistory } from 'react-router-dom';
import AttackTrainingModal from '../../components/AttackTrainingModal';
import DefenseTrainingModal from '../../components/DefenseTrainingModal';
import StamingTrainingModal from '../../components/StamingTrainingModal';
import SleepingModal from '../../components/SleepingModal';
import FeedingModal from '../../components/FeedingModal';
import ImproveModal from '../../components/ImproveModal';
import BurnModal from '../../components/BurnModal';
import AttackTrainingResultModal from '../../components/AttackTrainingResultModal';
import DefenseTrainingResultModal from '../../components/DefenseTrainingResultModal';
import StaminaTrainingResultModal from '../../components/StaminaTrainingResultModal';
import FeedingResultModal from '../../components/FeedingResultModal';
import SleepingResultModal from '../../components/SleepingResultModal';
import SkillsModal from '../../components/SkillsModal';
import ArrowRightIcon from '../../assets/images/arrow-right.svg';
import StarIcon from '../../assets/images/star.svg';
import AttackImage from '../../assets/images/attack.svg';
import EnergyImage from '../../assets/images/energy.png';
import HunterImage from '../../assets/images/hunter.svg';
import Defense from '../../assets/images/defense.svg';
import Stamina from '../../assets/images/stamina.svg';
import Fire from '../../assets/images/fire.svg';
import EggIcon from '../../assets/images/egg.svg';
import Diamond from '../../assets/images/diamond.svg';
import EditIcon from '../../assets/images/edit.svg';
import GoldStar from '../../assets/images/gold_star.png';
import FightLevel1 from '../../assets/images/fight-level1.png';
import FightLevel2 from '../../assets/images/fight-level2.png';
import FightLevel3 from '../../assets/images/fight-level3.png';
import FightLock from '../../assets/images/fight-lock.png';

const useStyles = makeStyles(theme => ({
    cardHeader: {
        padding: '12px',
        display: 'flex',
        justifyContent: 'space-between'
    },
    borderLinearProgress: {
        height: '6px!important',
        borderRadius: 3,
        margin: '6px 0',
        backgroundColor: '#EBEFF2!important',
        "& > span": {
            borderRadius: 3
        }
    },
    image: {
        marginRight: '4px'
    }
}));

const CardItem = ({icon, title, subtitle, number, color, value, buttonText, subText, onClick}) => {
    const classes = useStyles();
    return (
        <Box sx={{
            display: 'flex',
            flexDirection: { xs: 'column', sm: 'row', md: 'column', lg: 'row' },
            alignItems: 'center',
            marginBottom: '24px'
        }}>
            <Box sx={{
                flexGrow: '1',
                marginRight: { xs: 0, sm: '24px', md: 0, lg: '24px' },
                marginBottom: { xs: '16px', sm: 0, md: '16px', lg: 0 },
                width: { xs: '100%', sm: 'auto', md: '100%', lg: 'auto' }
            }}>
                <Box sx={{
                    display: 'flex',
                    alignItems: 'center',
                }}>
                    <img src={icon} alt="" />
                    <Typography
                        sx={{
                            fontSize: '12px',
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '18px',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            margin: '0 12px 0 8px'
                        }}
                    >
                        {title}
                    </Typography>
                    <Typography
                        sx={{
                            fontFamily: 'Poppins-Bold',
                            fontSize: '14px',
                            lineHeight: '21px',
                            display: 'flex',
                            alignItems: 'flex-end',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            flexGrow: 1
                        }}
                    >
                        {number}
                    </Typography>
                    <Typography
                        sx={{
                            fontSize: '10px',
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '15px',
                            textAlign: 'right',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            opacity: '0.5'
                        }}
                    >
                        {subtitle}
                    </Typography>
                </Box>
                <LinearProgress 
                    className={classes.borderLinearProgress} 
                    variant="determinate" 
                    value={value} 
                    sx={{
                        "& > span": {
                            backgroundColor: color
                        }
                    }}
                />
            </Box>
            <Button
                variant='contained'
                onClick={onClick}
                sx={{
                    width: { xs: '100%', sm: 183, md: '100%', lg: 183 },
                    backgroundColor: color,
                    boxShadow: '0px 2px 10px rgba(222, 117, 136, 0.08)',
                    fontSize: '12px',
                    fontFamily: 'Poppins-SemiBold',
                    letterSpacing: '0.01em',
                    textTransform: 'capitalize',
                    "&:hover": {
                        backgroundColor: color,
                    }
                }}
            >
                {buttonText}
                {subText && <Typography 
                    component="span"
                    sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        marginLeft: '4px'
                    }}>
                    {subText}
                </Typography>}
            </Button>
        </Box>
    )
}

const Level = ({ image, starNumber = 1, title, lock, onClick }) => {
    return (
        <Fragment>
            {lock ? (
                <Box sx={{
                    border: '3px dashed #EBEFF2',
                    borderRadius: '4px',
                    width: '110px',
                    maxWidth: '110px',
                    cursor: 'pointer',
                    p: '13px',
                }}>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        width: 50,
                        minWidth: 50,
                        height: 50,
                        borderRadius: '50%',
                        border: '1px solid #4C5862',
                        opacity: '0.5',
                        mb: '12px',
                        mx: 'auto'
                    }}>
                        <Box
                            component="img"
                            src={FightLock}
                            alt=""
                        />
                    </Box>
                    <Typography sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '15px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5'
                    }}>
                        Unlock
                    </Typography>
                    <Typography sx={{
                        fontSize: '12px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5'
                    }}>
                        Level 15
                    </Typography>
                </Box>
            ) : (
                <Box 
                    onClick={onClick} 
                    sx={{
                        boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                        width: '110px',
                        maxWidth: '110px',
                        borderRadius: '4px',
                        cursor: 'pointer',
                        p: '13px',
                        mb: '12px'
                    }}
                >
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        width: 50,
                        minWidth: 50,
                        height: 50,
                        borderRadius: '50%',
                        border: '1px solid #192A3E',
                        mb: '8px',
                        mx: 'auto'
                    }}>
                        <Box
                            component="img"
                            src={image}
                            alt=""
                        />
                    </Box>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'center',
                        mb: '4px'
                    }}>
                        {Array.from(Array(starNumber).keys()).map((index) => (
                            <Box 
                                key={index}
                                component="img"
                                src={GoldStar}
                                alt=""
                                sx={{
                                    mr: index !== (starNumber - 1) && '8px'
                                }}
                            />
                        ))}
                    </Box>
                    <Typography sx={{
                        fontSize: '12px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}>
                        {title}
                    </Typography>
                </Box>
            )}
        </Fragment>
    )
}

const Tab1Item = () => {
    const classes = useStyles();
    const history = useHistory();
    const [attackTrainingOpen, setAttackTrainingOpen] = useState(false);
    const [defenseTrainingOpen, setDefenseTrainingOpen] = useState(false);
    const [stamingTrainingOpen, setStamingTrainingOpen] = useState(false);
    const [sleepingOpen, setSleepingOpen] = useState(false);
    const [improveOpen, setImproveOpen] = useState(false);
    const [feedOpen, setFeedOpen] = useState(false);
    const [burnOpen, setBurnOpen] = useState(false);
    const [attackTrainingResultOpen, setAttackTrainingResultOpen] = useState(false);
    const [defenseTrainingResultOpen, setDefenseTrainingResultOpen] = useState(false);
    const [staminaTrainingResultOpen, setStaminaTrainingResultOpen] = useState(false);
    const [feedingResultOpen, setFeedingResultOpen] = useState(false);
    const [sleepingResultOpen, setSleepingResultOpen] = useState(false);
    const [skillsModalOpen, setSkillsModalOpen] = useState(false);
    return (
        <Fragment>
            <AttackTrainingModal open={attackTrainingOpen} onClose={() => setAttackTrainingOpen(false)} />
            <DefenseTrainingModal open={defenseTrainingOpen} onClose={() => setDefenseTrainingOpen(false)} />
            <StamingTrainingModal open={stamingTrainingOpen} onClose={() => setStamingTrainingOpen(false)} />
            <SleepingModal open={sleepingOpen} onClose={() => setSleepingOpen(false)} />
            <FeedingModal open={feedOpen} onClose={() => setFeedOpen(false)} />
            <ImproveModal open={improveOpen} onClose={() => setImproveOpen(false)} />
            <BurnModal open={burnOpen} onClose={() => setBurnOpen(false)} />
            <AttackTrainingResultModal open={attackTrainingResultOpen} onClose={() => setAttackTrainingResultOpen(false)} />
            <DefenseTrainingResultModal open={defenseTrainingResultOpen} onClose={() => setDefenseTrainingResultOpen(false)} />
            <StaminaTrainingResultModal open={staminaTrainingResultOpen} onClose={() => setStaminaTrainingResultOpen(false)} />
            <FeedingResultModal open={feedingResultOpen} onClose={() => setFeedingResultOpen(false)} />
            <SleepingResultModal open={sleepingResultOpen} onClose={() => setSleepingResultOpen(false)} />
            <SkillsModal open={skillsModalOpen} onClose={() => setSkillsModalOpen(false)} />
            <Box
                sx={{
                    padding: '24px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between'
                }}
            >
                <IconButton
                    onClick={() => history.goBack()}
                >
                    <img src={ArrowRightIcon} alt="" />
                </IconButton>
                <Box
                    sx={{
                        display: 'flex',
                        alignItems: 'center'
                    }}
                >
                    <img src={StarIcon} alt="" />
                    <Typography
                        sx={{
                            fontSize: '15px',
                            fontFamily: 'Poppins-Medium',
                            lineHeight: '22px',
                            letterSpacing: '0.01em',
                            color: '#323C47',
                            marginLeft: '8px'
                        }}
                    >
                        #00000
                    </Typography>
                </Box>
                <Box sx={{
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    <Typography
                        sx={{
                            fontSize: '14px',
                            lineHeight: '21px',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            marginRight: '4px',
                            fontFamily: 'Poppins-Bold',
                        }}
                    >
                        3
                    </Typography>
                    <img src={Diamond} alt="" />
                </Box>
            </Box>
            <Divider />
            <Box sx={{
                padding: { xs: "24px 24px", md: "24px 62px" }
            }}>
                <Box sx={{
                    aspectRatio: '1',
                    cursor: 'pointer',
                    width: '100%',
                    backgroundColor: 'rgba(36, 255, 0, 0.2);'
                }} />
                <Box sx={{
                    mt: '24px',
                    display: 'flex',
                    justifyContent: 'space-between',
                    flexWrap: 'wrap'
                }}>
                    <Level image={FightLevel1} starNumber={1} title="Attack +" lock={false} onClick={() => setSkillsModalOpen(true)} />
                    <Level image={FightLevel2} starNumber={2} title="Resistance" lock={false} onClick={() => setSkillsModalOpen(true)} />
                    <Level image={FightLevel3} starNumber={3} title="Dodge" lock={false} onClick={() => setSkillsModalOpen(true)} />
                    <Level lock={true} />
                </Box>
                <Box sx={{
                    mt: '24px'
                }}>
                    <CardItem 
                        icon={AttackImage} 
                        title="Attack" 
                        subtitle="(19,000 / 20,000 exp.)" 
                        number={7} color="#21866D" 
                        value={93} buttonText="Train" 
                        onClick={() => setAttackTrainingOpen(true)} 
                    />
                    <CardItem 
                        icon={Defense} 
                        title="Defense" 
                        subtitle="(1,000 / 40,000 exp.)" 
                        number={9} color="#BADB61" 
                        value={10} buttonText="Train" 
                        onClick={() => setDefenseTrainingOpen(true)}
                    />
                    <CardItem 
                        icon={Stamina} 
                        title="Stamina" 
                        subtitle="(9,900 / 10,000 exp.)" 
                        number={4} color="#33BC84" 
                        value={97} 
                        buttonText="Train" 
                        onClick={() => setStamingTrainingOpen(true)} 
                    />
                    <CardItem 
                        icon={EnergyImage} 
                        title="Energy" 
                        subtitle="(9/10)" 
                        color="#DE7588" 
                        value={90} 
                        buttonText="Sleep"
                        subText="(200 SOY)" 
                        onClick={() => setSleepingOpen(true)}
                    />
                    <CardItem 
                        icon={HunterImage} 
                        title="Hunger" 
                        subtitle="(1/10)" 
                        color="#105157" 
                        value={10} 
                        buttonText="Feed" 
                        onClick={() => setFeedOpen(true)} 
                    />
                    <CardItem 
                        icon={EggIcon} 
                        title="Egg Rarity :" 
                        subtitle="(1,000 / 40,000 SOY)" 
                        number="Common" 
                        color="#FEB12F" 
                        value={10} 
                        buttonText="Improve" 
                        subText="(200 SOY)" 
                        onClick={() => setImproveOpen(true)} 
                    />
                    <Divider />
                    <Button
                        fullWidth
                        variant="contained"
                        sx={{
                            background: '#044258!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mt: '24px'
                        }}
                        onClick={() => setBurnOpen(true)}
                    >
                        <img className={classes.image} src={EditIcon} alt="" />
                        Rename
                    </Button>
                    <Button
                        fullWidth
                        variant="contained"
                        sx={{
                            background: '#BF0C1A!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mt: '12px'
                        }}
                        onClick={() => setBurnOpen(true)}
                    >
                        <img className={classes.image} src={Fire} alt="" />
                        Burn
                    </Button>
                </Box>
            </Box>
        </Fragment>
    )
}

export default Tab1Item;