import React, { Fragment } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Typography,
    Box,
    LinearProgress
} from '@mui/material';
import { useHistory } from 'react-router-dom';
import StarIcon from '../assets/images/star.svg';
import AttackImage from '../assets/images/attack.svg';
import EnergyImage from '../assets/images/energy.png';
import HunterImage from '../assets/images/hunter.svg';
import Defense from '../assets/images/defense.svg';
import Stamina from '../assets/images/stamina.svg';
import Diamond from '../assets/images/diamond.svg';

const useStyles = makeStyles(theme => ({
    cardHeader: {
        padding: '12px',
        display: 'flex',
        justifyContent: 'space-between'
    },
    box: {
        display: 'flex',
        alignItems: 'center'
    },
    borderLinearProgress: {
        height: '6px!important',
        borderRadius: 3,
        margin: '6px 0',
        backgroundColor: '#EBEFF2!important',
        "& > span": {
            borderRadius: 3
        }
    }
}));

const CardItem = ({icon, title, subtitle, number, color, value}) => {
    const classes = useStyles();
    return (
        <Box sx={{marginBottom: '12px'}}>
            <Box className={classes.box}>
                <img src={icon} alt="" />
                <Typography
                    sx={{
                        fontSize: '12px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        margin: '0 12px 0 8px'
                    }}
                >
                    {title}
                </Typography>
                <Typography
                    sx={{
                        fontFamily: 'Poppins-Bold',
                        fontSize: '14px',
                        lineHeight: '21px',
                        display: 'flex',
                        alignItems: 'flex-end',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        flexGrow: 1
                    }}
                >
                    {number}
                </Typography>
                <Typography
                    sx={{
                        fontSize: '10px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '15px',
                        textAlign: 'right',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5'
                    }}
                >
                    {subtitle}
                </Typography>
            </Box>
            <LinearProgress 
                className={classes.borderLinearProgress} 
                variant="determinate" 
                value={value} 
                sx={{
                    "& > span": {
                        backgroundColor: color
                    }
                }}
            />
        </Box>
    )
}

const TitleItem = ({index}) => {
    const classes = useStyles();
    const history = useHistory();
    return (
        <Fragment>
            <Box className={classes.cardHeader}>
                <Box
                    sx={{
                        display: 'flex',
                        alignItems: 'center'
                    }}
                >
                    <img src={StarIcon} alt="" />
                    <Typography
                        sx={{
                            fontSize: '15px',
                            fontFamily: 'Poppins-Medium',
                            lineHeight: '22px',
                            letterSpacing: '0.01em',
                            color: '#323C47',
                            marginLeft: '8px'
                        }}
                    >
                        #00000
                    </Typography>
                </Box>
                <Box className={classes.box}>
                    <Typography
                        sx={{
                            fontSize: '14px',
                            lineHeight: '21px',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            marginRight: '4px',
                            fontFamily: 'Poppins-Bold',
                        }}
                    >
                        3
                    </Typography>
                    <img src={Diamond} alt="" />
                </Box>
            </Box>
            <Box
                sx={{
                    aspectRatio: '1',
                    cursor: 'pointer',
                    width: '100%',
                    backgroundColor: 'rgba(36, 255, 0, 0.2);'
                }}
                onClick={() => history.push(`/fight-club/tab1/${index + 1}`)}
            ></Box>
            <Box
                sx={{
                    padding: '24px'
                }}
            >
                <CardItem icon={AttackImage} title="Attack" subtitle="(19,000 / 20,000 exp.)" number={7} color="#21866D" value={93} />
                <CardItem icon={Defense} title="Defense" subtitle="(1,000 / 40,000 exp.)" number={9} color="#BADB61" value={10} />
                <CardItem icon={Stamina} title="Stamina" subtitle="(9,900 / 10,000 exp.)" number={4} color="#33BC84" value={97} />
                <CardItem icon={EnergyImage} title="Energy" subtitle="(9/10)" color="#DE7588" value={90} />
                <CardItem icon={HunterImage} title="Hunger" subtitle="(1/10)" color="#105157" value={10} />
            </Box>
        </Fragment>
    )
}

export default TitleItem;