import React, { forwardRef, useState } from 'react';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Slide,
    Box,
    IconButton,
    Divider,
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import FarmImage from '../assets/images/farm.png';
import AttackImage from '../assets/images/attack.svg';
import Defense from '../assets/images/defense.svg';

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const ResisterItem = ({ resistered, selected, onSelect }) => {
    const onItemSelect = () => {
        if (!resistered) {
            onSelect()
        }
    }
    return (
        <Box 
            sx={{
                cursor: 'pointer',
                position: 'relative',
                boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                borderRadius: '4px',
                width: { xs: 'calc(50% - 12px)', sm: 'calc(33% - 16px)' },
                pt: '14px',
                pb: '20px',
                mb: '24px',
            }}
            onClick={onItemSelect}
        >
            <Box sx={{
                position: 'absolute',
                top: '-2px',
                right: '-2px',
                width: '92px',
                height: '92px',
                overflow: 'hidden',
                "&:before": {
                    content: "''",
                    backgroundColor: resistered ? "#044258" : selected ? "#CD1322" : null,
                    position: 'absolute',
                    top: '0px',
                    right: '47px',
                    width: '40px',
                    height: '2px',
                    borderRadius: '2px 0 0 0',

                },
                "&:after": {
                    content: "''",
                    backgroundColor: resistered ? "#044258" : selected ? "#CD1322" : null,
                    position: 'absolute',
                    top: '46px',
                    right: '1px',
                    width: '40px',
                    height: '2px',
                    transformOrigin: 'right',
                    transform: 'rotate(-90deg)',
                    borderRadius: '0 0px 0 2px',
                }
            }}>
                <Box sx={{
                        position: 'absolute',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        fontFamily: 'Poppins-Medium',
                        background: resistered ? "#044258" : selected ? "#CD1322" : null,
                        right: '-49px',
                        top: '15px',
                        fontSize: '11px',
                        color: '#fff',
                        transform: 'rotate(45deg)',
                        width: '160px',
                        height: '32px',
                }}>
                    {resistered ? "Registered" : selected ? "Selected" : null}
                </Box>
            </Box>
            <Typography
                sx={{
                    fontSize: '15px',
                    fontFamily: 'Poppins-Medium',
                    textAlign: 'center',
                    lineHeight: '22px',
                    letterSpacing: '0.01em',
                    color: '#323C47',
                    mb: '11px'
                }}
            >
                #00000
            </Typography>
            <Box sx={{
                aspectRatio: '1',
                backgroundColor: '#64EC4E'
            }}></Box>
            <Box sx={{
                mt: '16px',
                px: '10px',
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center'
            }}>
                <Box sx={{
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    <img src={AttackImage} alt="" />
                    <Typography sx={{
                        fontSize: 14,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '21px',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        ml: '8px'
                    }}>
                        Level <b>4</b>
                    </Typography>
                </Box>
                <Box sx={{
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    <img src={Defense} alt="" />
                    <Typography sx={{
                        fontSize: 14,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '21px',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        ml: '8px'
                    }}>
                        Level <b>3</b>
                    </Typography>
                </Box>
            </Box>
        </Box>
    )
}

const ResisterModal = ({open, onClose}) => {
    const [selectId, setSelectId] = useState(null);
    return (
        <Dialog 
            TransitionComponent={Transition}
            onClose={onClose} 
            open={open} 
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 636,
                    width: '100%'
                }
            }}
        >
            <Paper>
                <Box sx={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    p: '8px 24px'
                }}>
                    <Box></Box>
                    <Typography sx={{
                        fontSize: '15px',
                        fontFamily: 'Poppins-Medium',
                        lineHeight: '22px',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}>
                        Register your Roosters
                    </Typography>
                    <IconButton size="small" onClick={onClose}>
                        <CloseIcon />
                    </IconButton>
                </Box>
                <Divider />
                <Box sx={{ p: '24px' }}>
                    <Box sx={{ position: 'relative' }}>
                        <Box
                            component="img"
                            src={FarmImage}
                            alt=""
                            width="100%"
                            sx={{ 
                                borderRadius: '4px',
                                display: 'block'
                            }}
                        />
                        <Box
                            sx={{
                                position: 'absolute',
                                top: 0,
                                left: 0,
                                right: 0,
                                bottom: 0,
                                backgroundColor: 'rgba(0, 0, 0, 0.5)'
                            }}
                        ></Box>
                        <Box
                            sx={{
                                position: 'absolute',
                                top: '50%',
                                left: '50%',
                                transform: 'translate(-50%, -50%)'
                            }}
                        >
                            <Typography sx={{
                                fontSize: 15,
                                lineHeight: '22px',
                                textAlign: 'center',
                                letterSpacing: '0.01em',
                                color: '#FFFFFF',
                                fontFamily: 'Poppins-Bold',
                                whiteSpace: 'nowrap',
                                mb: '4px'
                            }}>
                                Saturday, 12th March
                            </Typography>
                            <Typography
                                sx={{
                                    fontSize: 18,
                                    lineHeight: '27px',
                                    textAlign: 'center',
                                    letterSpacing: '0.01em',
                                    color: '#FFFFFF',
                                    whiteSpace: 'nowrap',
                                    fontFamily: 'Poppins-Regular'
                                }}
                            >
                                6 Days 8 Hours 40 Mins 
                            </Typography>
                        </Box>
                    </Box>
                    <Typography sx={{
                        fontSize: 15,
                        lineHeight: '22px',
                        fontFamily: 'Poppins-Bold',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                        mt: '24px',
                        mb: '5px'
                    }}>
                        Saturday, 12th March
                    </Typography>
                    <Typography sx={{
                        fontSize: 14,
                        lineHeight: '21px',
                        textAlign: 'center',
                        fontFamily: 'Poppins-Regular',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        mb: '24px'
                    }}>
                        Select the roosters you want to register for the next tournament
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        flexWrap: 'wrap',
                        justifyContent: 'space-between',
                    }}>
                        {Array.from(Array(6).keys()).map((index) => (
                            <ResisterItem 
                                key={index} 
                                resistered={index === 0}
                                selected={selectId === index}
                                onSelect={() => setSelectId(index)}
                            />
                        ))}
                    </Box>
                    <Box sx={{
                        display: 'flex',
                        flexDirection: { xs: 'column', sm: 'row' }
                    }}>
                        <Button
                            variant='outlined'
                            onClick={onClose}
                            sx={{
                                color: '#CD1322',
                                fontFamily: "Poppins-SemiBold",
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                borderColor: '#CD1322',
                                minHeight: '37px',
                                width: { xs: '100%', sm: 137 },
                                mb: { xs: '16px', sm: 0 },
                                mr: '24px',
                                "&:hover": {
                                    borderColor: '#CD1322',
                                }
                            }}
                        >
                            Cancel
                        </Button>
                        <Button 
                            variant='contained'
                            onClick={onClose}
                            sx={{
                                flexGrow: '1',
                                color: '#fff!important',
                                background: '#CD1322!important',
                                boxShadow: '0px 2px 10px rgba(4, 66, 88, 0.08)',
                                fontFamily: "Poppins-SemiBold",
                                fontSize: '12px',
                                letterSpacing: '0.01em',
                                textTransform: 'capitalize',
                                minHeight: '37px',
                            }}
                        >
                            Register
                            <Typography sx={{
                                fontSize: '12px',
                                fontFamily: 'Poppins-Regular'
                            }}>
                                (2 MATIC)
                            </Typography>
                        </Button>
                    </Box>
                </Box>
            </Paper>
        </Dialog>
    );
}

export default ResisterModal;