import React, { forwardRef, useState, useEffect } from 'react';
import { 
    Dialog,
    Paper,
    Slide,
    Typography,
    Box,
    Button
} from '@mui/material';
import Timer from './Timer';
import IlluCornoids from '../assets/images/illu_cornoids4-24.png';

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const DefenseTrainingModal = ({open, onClose}) => {
    const [isActive, setIsActive] = useState(false);
    const [showCornoids, setShowCornoids] = useState(false);
    const [isStart, setIsStart] = useState(false);
    useEffect(() => {
        if (open) setIsStart(true)
    }, [open])
    return (
        <Dialog 
            onClose={onClose} 
            open={open} 
            TransitionComponent={Transition}
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper sx={{
                p: '46px 24px 24px'
            }}>
                <Typography sx={{
                   fontSize: 15,
                   fontFamily: 'Poppins-Bold',
                   lineHeight: '22px',
                   textAlign: 'center',
                   letterSpacing: '0.01em',
                   color: '#192A3E',
                }}>
                    Stack to Boost your Training Timings
                </Typography>
                <Typography sx={{
                    fontSize: 12,
                    fontFamily: 'Poppins-Regular',
                    lineHeight: '18px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: '#4C5862',
                    mb: '16px'
                }}>
                    Stack <b>250 MATIC</b> to increase your timings <br />
                    for unstacking
                </Typography>
                <Box sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    mb: '16px'
                }}>
                    <Timer isActive={isActive} timeFrame={160} setShowCornoids={setShowCornoids} />
                </Box>
                <Button
                    fullWidth
                    variant='contained'
                    sx={{
                        background: '#BF0C1A!important',
                        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                        fontFamily: "Poppins-SemiBold",
                        fontSize: '12px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        minHeight: '37px',
                    }}
                    onClick={() => {
                        setIsActive(isActive => !isActive);
                        setIsStart(false);
                    }}
                >
                    {isStart ? 'Start Training' : isActive ? 'Stop Training' : 'Continue'}
                </Button>
                {showCornoids && <Box>
                    <Typography sx={{
                        fontSize: 12,
                        position: 'relative',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#A6ACB1',
                        my: '16px',
                        "&:before": {
                            content: "''",
                            position: 'absolute',
                            top: 'calc(50% - 1px)',
                            height: '2px',
                            background: '#EBEFF2',
                            left: '0',
                            width: 'calc(50% - 20px)',
                        },
                        "&:after": {
                            content: "''",
                            position: 'absolute',
                            top: 'calc(50% - 1px)',
                            height: '2px',
                            background: '#EBEFF2',
                            right: '0',
                            width: 'calc(50% - 20px)',
                        }
                    }}>
                        OR
                    </Typography>
                    <Typography sx={{
                        fontSize: 12,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}>
                        Buy a shot of Cornoids for perfect training stats
                    </Typography>
                    <Typography sx={{
                        fontSize: 10,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        opacity: '0.5'
                    }}>
                        (Get your XP and your energy)
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'center'
                    }}>
                        <img src={IlluCornoids} alt="" />
                    </Box>
                    <Button
                        fullWidth
                        variant='contained'
                        onClick={onClose}
                        sx={{
                            background: '#BF0C1A!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: "Poppins-SemiBold",
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mb: '12px'
                        }}
                    >
                        Buy Cornoids Shot
                    </Button>
                    <Button
                        variant='outlined'
                        fullWidth
                        onClick={onClose}
                        sx={{
                            color: '#CD1322',
                            fontFamily: "Poppins-SemiBold",
                            fontSize: '12px',
                            minHeight: '37px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            borderColor: '#CD1322',
                            "&:hover": {
                                borderColor: '#CD1322',
                            }
                        }}
                    >
                        Cancel
                    </Button>
                </Box>}
            </Paper>
        </Dialog>
    );
}

export default DefenseTrainingModal;