import React, { Fragment, useState } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Typography,
    Divider,
    Box,
    IconButton,
    LinearProgress,
    Button
} from '@mui/material';
import { useHistory } from 'react-router-dom';
import ImproveModal from '../../components/ImproveModal';
import StakeModal from '../../components/StakeModal';
import BurnModal from '../../components/BurnModal';
import CornoidsModal from '../../components/CornoidsModal';
import BreedingModal from '../../components/BreedingModal';
import ArrowRightIcon from '../../assets/images/arrow-right.svg';
import StarIcon from '../../assets/images/star.svg';
import EditIcon from '../../assets/images/edit.svg';
import Fire from '../../assets/images/fire.svg';
import EggIcon from '../../assets/images/egg.svg';
import Egg2Icon from '../../assets/images/egg2.svg';

const useStyles = makeStyles(theme => ({
    cardHeader: {
        padding: '12px',
        display: 'flex',
        justifyContent: 'space-between'
    },
    borderLinearProgress: {
        height: '6px!important',
        borderRadius: 3,
        margin: '6px 0',
        backgroundColor: '#EBEFF2!important',
        "& > span": {
            borderRadius: 3
        }
    },
    image: {
        marginRight: '4px'
    }
}));

const CardItem = ({icon, title, subtitle, number, color, value, buttonText, subText, disabled, onClick}) => {
    const classes = useStyles();
    return (
        <Box sx={{
            display: 'flex',
            flexDirection: { xs: 'column', sm: 'row', md: 'column', lg: 'row' },
            alignItems: 'center',
            marginBottom: '24px'
        }}>
            <Box sx={{
                flexGrow: '1',
                marginRight: { xs: 0, sm: '24px', md: 0, lg: '24px' },
                marginBottom: { xs: '16px', sm: 0, md: '16px', lg: 0 },
                width: { xs: '100%', sm: 'auto', md: '100%', lg: 'auto' }
            }}>
                <Box sx={{
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    <img src={icon} alt="" />
                    <Typography
                        sx={{
                            fontSize: '12px',
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '18px',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            margin: '0 12px 0 8px'
                        }}
                    >
                        {title}
                    </Typography>
                    <Typography
                        sx={{
                            fontFamily: 'Poppins-Bold',
                            fontSize: '14px',
                            lineHeight: '21px',
                            display: 'flex',
                            alignItems: 'flex-end',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            flexGrow: 1
                        }}
                    >
                        {number}
                    </Typography>
                    <Typography
                        sx={{
                            fontSize: '10px',
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '15px',
                            textAlign: 'right',
                            letterSpacing: '0.01em',
                            color: '#4C5862',
                            opacity: '0.5'
                        }}
                    >
                        {subtitle}
                    </Typography>
                </Box>
                <LinearProgress 
                    className={classes.borderLinearProgress} 
                    variant="determinate" 
                    value={value} 
                    sx={{
                        "& > span": {
                            backgroundColor: color
                        }
                    }}
                />
            </Box>
            <Button
                variant='contained'
                disabled={disabled}
                onClick={onClick}
                sx={{
                    width: { xs: '100%', sm: 183, md: '100%', lg: 183 },
                    backgroundColor: disabled ? '#C4C4C4!important' : color,
                    boxShadow: '0px 2px 10px rgba(222, 117, 136, 0.08)',
                    fontSize: '12px',
                    fontFamily: 'Poppins-SemiBold',
                    color: '#fff!important',
                    letterSpacing: '0.01em',
                    textTransform: 'capitalize',
                    "&:hover": {
                        backgroundColor: disabled ? '#C4C4C4!important' : color,
                    }
                }}
            >
                {disabled ? (
                    <>
                        <img style={{marginRight: '8px'}} src={Egg2Icon} alt="" />
                        Harvest
                    </>
                ) : (
                    <>
                        {buttonText}
                        {subText && <Typography 
                            component="span"
                            sx={{
                                fontSize: '10px',
                                fontFamily: 'Poppins-Regular',
                                marginLeft: '4px'
                            }}>
                            {subText}
                        </Typography>}
                    </>
                )}
                
            </Button>
        </Box>
    )
}

const Tab2Item = () => {
    const classes = useStyles();
    const history = useHistory();
    const [improveOpen, setImproveOpen] = useState(false);
    const [stakeOpen, setStakeOpen] = useState(false);
    const [burnOpen, setBurnOpen] = useState(false);
    const [cornoidsOpen, setCornoidsOpen] = useState(false);
    const [breedingOpen, setBreedingOpen] = useState(false);
    return (
        <Fragment>
            <ImproveModal open={improveOpen} onClose={() => setImproveOpen(false)} />
            <StakeModal open={stakeOpen} onClose={() => setStakeOpen(false)} />
            <BurnModal open={burnOpen} onClose={() => setBurnOpen(false)} />
            <CornoidsModal open={cornoidsOpen} onClose={() => setCornoidsOpen(false)} />
            <BreedingModal open={breedingOpen} onClose={() => setBreedingOpen(false)} />
            <Box
                sx={{
                    padding: '24px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between'
                }}
            >
                <IconButton
                    onClick={() => history.goBack()}
                >
                    <img src={ArrowRightIcon} alt="" />
                </IconButton>
                <Box
                    sx={{
                        display: 'flex',
                        alignItems: 'center'
                    }}
                >
                    <img src={StarIcon} alt="" />
                    <Typography
                        sx={{
                            fontSize: '15px',
                            fontFamily: 'Poppins-Medium',
                            lineHeight: '22px',
                            letterSpacing: '0.01em',
                            color: '#323C47',
                            marginLeft: '8px'
                        }}
                    >
                        #00000
                    </Typography>
                </Box>
                <Box></Box>
            </Box>
            <Divider />
            <Box sx={{
                padding: { xs: "24px 24px", md: "24px 62px" }
            }}>
                <Box sx={{
                    aspectRatio: '1',
                    cursor: 'pointer',
                    width: '100%',
                    backgroundColor: 'rgba(36, 255, 0, 0.2);'
                }} />
                <Box sx={{
                    marginTop: '24px'
                }}>
                    <CardItem icon={EggIcon} title="Egg Rate :" subtitle="(8 hours 40 mins)" number="x1" color="#DE7588 " value={20} buttonText="Improve" subText="(200 SOY)" disabled={true} />
                    <CardItem icon={EggIcon} title="Egg Rarity :" subtitle="(1,000 / 40,000 SOY)" number="Common" color="#FEB12F" value={10} buttonText="Improve" subText="(200 SOY)" onClick={() => setImproveOpen(true)} />
                    <Divider />
                    <Button
                        fullWidth
                        variant="contained"
                        sx={{
                            background: '#DE7588!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mt: '24px'
                        }}
                        onClick={() => setBreedingOpen(true)}
                    >
                        Breeding
                    </Button>
                    <Button
                        fullWidth
                        variant="contained"
                        sx={{
                            background: '#105157!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mt: '12px',
                            mb: '24px'
                        }}
                        onClick={() => setCornoidsOpen(true)}
                    >
                        Boost Egg Production
                    </Button>
                    <Divider />
                    <Button
                        fullWidth
                        variant="contained"
                        sx={{
                            background: '#044258!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mt: '24px'
                        }}
                        onClick={() => setBurnOpen(true)}
                    >
                        <img className={classes.image} src={EditIcon} alt="" />
                        Rename
                    </Button>
                    <Button
                        fullWidth
                        variant="contained"
                        sx={{
                            background: '#BF0C1A!important',
                            boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                            fontFamily: 'Poppins-SemiBold',
                            fontSize: '12px',
                            letterSpacing: '0.01em',
                            textTransform: 'capitalize',
                            minHeight: '37px',
                            mt: '12px'
                        }}
                        onClick={() => setBurnOpen(true)}
                    >
                        <img className={classes.image} src={Fire} alt="" />
                        Burn
                    </Button>
                </Box>
            </Box>
        </Fragment>
    )
}

export default Tab2Item;