import React, { Fragment } from 'react';
import { 
    Typography,
    Divider,
    Box,
} from '@mui/material';
import TitleItem from '../../components/TitleItem'

const Tab1 = () => {
    return (
        <Fragment>
            <Typography sx={{
               fontSize: '15px',
               fontFamily: 'Poppins-Medium',
               lineHeight: '22px',
               letterSpacing: '0.01em',
               color: '#192A3E',
               padding: '12px 24px 8px'
            }}>
               Title
            </Typography>
            <Divider />
            <Box
                sx={{
                    p: '24px 24px 0',
                    display: 'flex',
                    justifyContent: 'space-between',
                    flexWrap: 'wrap'
                }}
            >
                {[0, 1, 2, 3].map((index => (
                    <Box
                        key={index}
                        sx={{
                            boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.08)',
                            borderRadius: '4px',
                            width: {xs: '100%', sm: 'calc(50% - 12px)'},
                            mb: '24px'
                        }}
                    >
                        <TitleItem index={index} />
                    </Box>
                )))}
            </Box>
        </Fragment>
    )
}

export default Tab1;