import React from 'react'
import { Box } from '@mui/material';

const LineProgressBar1 = ({ value1, value2, color }) => (
    <Box sx={{
        height: '6px',
        backgroundColor: '#EBEFF2',
        borderRadius: '3px',
        position: 'relative',
        overflow: 'hidden'
    }}>
        <Box sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            borderRadius: '3px',
            backgroundColor: color,
            opacity: '0.5',
            transform: `translateX(${ value2  - 100}%)`
        }}></Box>
        <Box sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            borderRadius: '3px',
            backgroundColor: color,
            transform: `translateX(${ value1  - 100}%)`
        }}></Box>
    </Box>
)
export default LineProgressBar1;