import React, { forwardRef } from 'react';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Slide,
    Box
} from '@mui/material';
import Cornoids1 from '../assets/images/cornoids1.png';
import Cornoids2 from '../assets/images/cornoids2.png';

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const CornoidsModal = ({open, onClose}) => {
    return (
        <Dialog 
            TransitionComponent={Transition}
            onClose={onClose} 
            open={open} 
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper sx={{
                padding: '46px 24px 24px'
            }}>
                <Typography
                    sx={{
                        fontFamily: "Poppins-Bold",
                        fontSize: '15px',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}
                >
                    Boost your Egg Production
                </Typography>
                <Typography
                    sx={{
                        fontSize: '12px',
                        lineHeight: '18px',
                        fontFamily: "Poppins-Regular",
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}
                >
                    Buy a shot of Cornoids to improve by <b> 2 </b>your EggRate
                    <Typography
                        component="span"
                        sx={{
                            fontSize: 10,
                            fontFamily: 'Poppins-Regular',
                            opacity: '0.8',
                            display: 'block'
                        }}
                    >
                        (Effective 24 hours)
                    </Typography>
                </Typography>
                <Box sx={{
                    display: 'flex',
                    justifyContent: 'center'
                }}>
                    <img src={Cornoids1} alt="" />
                </Box>
                <Button 
                    fullWidth
                    variant='contained'
                    sx={{
                        background: '#BF0C1A!important',
                        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                        fontFamily: "Poppins-SemiBold",
                        fontSize: '12px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        minHeight: '37px',
                        mb: '24px'
                    }}
                    onClick={onClose}
                >
                    Buy Cornoids Shot
                </Button>
                <Typography sx={{
                    fontSize: '12px',
                    lineHeight: '18px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: '#A6ACB1',
                    fontFamily: 'Poppins-Regular',
                    textTransform: 'uppercase',
                    mb: '12px'
                }}>
                    or
                </Typography>
                <Typography
                    sx={{
                        fontSize: '12px',
                        lineHeight: '18px',
                        fontFamily: "Poppins-Regular",
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}
                >
                    Buy a Bottle of Cornoids to improve by <b> 2 </b> your EggRate
                    <Typography
                        component="span"
                        sx={{
                            fontSize: 10,
                            fontFamily: 'Poppins-Regular',
                            opacity: '0.8',
                            display: 'block'
                        }}
                    >
                        (Effective 7 days)
                    </Typography>
                </Typography>
                <Box sx={{
                    display: 'flex',
                    justifyContent: 'center'
                }}>
                    <img src={Cornoids2} alt="" />
                </Box>
                <Button 
                    fullWidth
                    variant='contained'
                    sx={{
                        background: '#BF0C1A!important',
                        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)',
                        fontFamily: "Poppins-SemiBold",
                        fontSize: '12px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        minHeight: '37px',
                        mb: '12px'
                    }}
                    onClick={onClose}
                >
                    Buy Cornoids Bottle
                </Button>
                <Button
                    variant='outlined'
                    fullWidth
                    onClick={onClose}
                    sx={{
                        color: '#CD1322',
                        fontFamily: "Poppins-SemiBold",
                        fontSize: '12px',
                        minHeight: '37px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        borderColor: '#CD1322',
                        "&:hover": {
                            borderColor: '#CD1322',
                        }
                    }}
                >
                    Cancel
                </Button>
            </Paper>
        </Dialog>
    );
}

export default CornoidsModal;