import React from 'react'
import { Box } from '@mui/material';

const LineProgressBar2 = ({ value1, value2, color }) => (
    <Box sx={{
        height: '6px',
        backgroundColor: '#EBEFF2',
        borderRadius: '3px',
        position: 'relative',
        overflow: 'hidden'
    }}>
        <Box sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            borderRadius: '3px',
            backgroundColor: '#EBEFF2',
            transform: `translateX(${ value2  - 100}%)`,
            overflow: 'hidden'
        }}>
            {Array.from(Array(155).keys()).map((index) => (
                <Box
                    key={index}
                    component="span"
                    sx={{
                        position: 'absolute',
                        top: '-50%',
                        left: `${3 * index}px`,
                        width: '1px',
                        height: '200%',
                        borderLeft: `1px solid ${color}`,
                        transform: 'rotate(45deg)'
                    }}
                ></Box>
            ))}
        </Box>
        <Box sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            borderRadius: '3px',
            backgroundColor: color,
            transform: `translateX(${ value1  - 100}%)`
        }}></Box>
    </Box>
)
export default LineProgressBar2;