import React, { Fragment, useEffect, useState } from 'react';
import { Typography, Box } from '@mui/material';
import Defense from '../assets/images/defense.svg';
import CustomToolTip from './CustomTooltip';

const frame = 320;

const Timer = ( {isActive, timeFrame, setShowCornoids} ) => {
    const circleRound = 100;
    const [timeAngle, setTimeAngle] = useState(0);
    const [strokeColor, setStrokeColor] = useState('#929292');

    const [time, setTime] = useState(frame);

    const [timeInterval, setTimeInterval] = useState(null)

    useEffect(() => {
        return () => clearInterval(timeInterval)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        if (isActive) {
            timeStart()
        } else {
            timeStop()
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isActive])

    useEffect(() => {
        if (timeAngle > 320) {
            clearInterval(timeInterval)
        }
        if (timeAngle < 220) setStrokeColor('#929292');
        if (timeAngle >= 220 && timeAngle < 240) setStrokeColor('#FEB12F');
        if (timeAngle >= 240 && timeAngle < 260) setStrokeColor('#33BC84');
        if (timeAngle >= 260 && timeAngle < 280) {
            setStrokeColor('#FEB12F');
            setShowCornoids(true);
        }
        if (timeAngle >= 280) setStrokeColor('#CD1322');
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [timeAngle])
    
    function timeStart() {
        if (timeAngle < 320) {
            setTimeInterval(setInterval(() => {
                setTimeAngle(timeAngle => timeAngle + 320 / frame);
                setTime(time => time > 0 && (time - 1));
            }, 1000))
        }
    }

    function timeStop() {
        clearInterval(timeInterval)
    }

    const lineColorArray = [
        '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF',
        '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF',
        '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF',
        '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF', '#ECEDEF',
        '#ECEDEF', '#ECEDEF', '#ECEDEF', '#FEB12F', '#FEB12F', '#FEB12F', '#FEB12F', '#33BC84', '#33BC84', '#33BC84',
        '#33BC84', '#FEB12F', '#FEB12F', '#FEB12F', '#FEB12F', '#CD1322', '#CD1322', '#CD1322', '#CD1322', '#CD1322',
        '#CD1322', '#CD1322', '#CD1322',
    ]

    return (
        <Box sx={{
            position: 'relative',
            width: 330,
            height: 330
        }}>
            <svg width="100%" height="100%" viewBox="-65 -65 330 330">
                <circle 
                    stroke="#ECEDEF"
                    cx={circleRound}
                    cy={circleRound}
                    r={circleRound}
                    transform={`rotate(110 ${circleRound} ${circleRound})`}
                    strokeDasharray={Math.PI * 2 * circleRound}
                    strokeWidth="6"
                    strokeLinecap="round"
                    strokeDashoffset={2 * Math.PI * circleRound * 40 / 360}
                    fill="none"
                ></circle>
                <circle 
                    stroke={strokeColor}
                    transform={`rotate(110 ${circleRound} ${circleRound})`}
                    cx={circleRound}
                    cy={circleRound}
                    r={circleRound}
                    strokeDasharray={Math.PI * 2 * circleRound}
                    strokeWidth="7"
                    strokeDashoffset={2 * Math.PI * circleRound * ( 360 - timeAngle ) / 360}
                    strokeLinecap="round"
                    fill="none"
                    style={{transition: 'stroke-dashoffset 1s linear 0s'}}
                ></circle>
                <circle 
                    transform={`rotate(${timeAngle - 160} ${circleRound} ${circleRound})`}
                    cx={circleRound}
                    cy="0"
                    r="7.3"
                    fill={strokeColor}
                    style={{transition: 'transform 1s linear 0s'}}
                ></circle>
                {lineColorArray.map((color, key) => (
                    <Fragment key={key}>
                        <line
                            transform={`rotate(${-245 + 5 * key} 100 100)`}
                            x1="230"
                            y1="100"
                            x2="238"
                            y2="100"
                            stroke={color}
                            strokeWidth="2"
                        ></line>
                        <CustomToolTip title={
                            <Box sx={{
                                px: '8px'
                            }}>
                                <Box sx={{
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    mb: '10px'
                                }}>
                                    <Box sx={{
                                        width: 10,
                                        height: 10,
                                        borderRadius: '50%',
                                        background: key < 43 ? '#ECEDEF' : key < 47 ? '#FEB12F' : key < 51 ? '#B0DE58' : key < 55 ? '#FEB12F' : '#CD1322',
                                        mr: '8px'
                                    }}></Box>
                                    <Typography sx={{
                                        fontFamily: 'Poppins-Bold',
                                        fontWeight: 'bold',
                                        fontSize: '12px',
                                        lineHeight: '12px',
                                        letterSpacing: '0.5px',
                                        color: '#000000',
                                    }}>
                                        { 
                                            key < 43 ? '< ' + timeFrame * 11 / 16 + 'min' : 
                                            key < 47 ? timeFrame * 11 / 16 + '- ' + timeFrame * 3 / 4 + 'min' : 
                                            key < 51 ? timeFrame * 3 / 4 + ' - ' + timeFrame * 13 / 16 + 'min' : 
                                            key < 55 ? timeFrame * 13 / 16 + ' - ' + timeFrame * 7 / 8 + 'min': '> ' + timeFrame * 7 / 8 + 'min' 
                                        }
                                    </Typography>
                                </Box>
                                <Typography sx={{
                                    fontSize: '12px',
                                    fontFamily: 'Poppins-Regular',
                                    lineHeight: '15px',
                                    textAlign: 'center',
                                    color: '#121212',
                                    opacity: '0.7',
                                }}>
                                    {key < 43 ? '-50% Energy cost' : key < 47 ? '-50% Training Exp.' : key < 51 ? '100% Exp' : key < 55 ? '-50% Training Exp.' : '-2 x Energy Cost No Exp.'}
                                </Typography>
                            </Box>
                        } placement={key < 32 ? 'right' : 'left'}>
                            <rect x="228" y="100" width="12" height="11" fill="transparent" transform={`rotate(${-245 + 5 * key} 100 100)`} />
                        </CustomToolTip>
                    </Fragment>
                ))}
                <CustomToolTip title={
                    <Box sx={{
                        px: '16px'
                    }}>
                        <Box sx={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            mb: '10px'
                        }}>
                            <Box sx={{
                                width: 10,
                                height: 10,
                                borderRadius: '50%',
                                background: '#ECEDEF',
                                mr: '8px'
                            }}></Box>
                            <Typography sx={{
                                fontFamily: 'Poppins-Bold',
                                fontWeight: 'bold',
                                fontSize: '12px',
                                lineHeight: '12px',
                                letterSpacing: '0.5px',
                                color: '#000000',
                            }}>
                                {'< ' + timeFrame * 11 / 16 + 'min'}
                            </Typography>
                        </Box>
                        <Typography sx={{
                            fontSize: '12px',
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '15px',
                            textAlign: 'center',
                            color: '#121212',
                            opacity: '0.7',
                        }}>
                            -50% Energy cost
                        </Typography>
                    </Box>
                } placement="right">
                    <rect x="228" y="100" width="12" height="11" fill="transparent" transform={`rotate(-250 100 100)`} />
                </CustomToolTip>
                <line
                    transform={`rotate(-250 100 100)`}
                        x1="224"
                        y1="100"
                        x2="238"
                        y2="100"
                        stroke="#ECEDEF"
                        strokeWidth="2"
                ></line>
                <line
                    transform={`rotate(70 100 100)`}
                    x1="224"
                    y1="100"
                    x2="238"
                    y2="100"
                    stroke="#CD1322"
                    strokeWidth="2"
                ></line>
                <text x="50" y="245" fontSize="12px" fontFamily='Poppins-Regular' letterSpacing='0.01em' stroke="#4c5862" opacity="0.5">
                    0
                </text>
                <text x="7" y="-20" fontSize="12px" fontFamily='Poppins-Regular' letterSpacing='0.01em' stroke="#4c5862" opacity="0.5">
                    {timeFrame * 3 / 8}
                </text>
                <text x="240" y="80" fontSize="12px" fontFamily='Poppins-Regular' letterSpacing='0.01em' stroke="#4c5862" opacity="0.5">
                    {timeFrame * 3 / 4}
                </text>
                <text x="135" y="245" fontSize="12px" fontFamily='Poppins-Regular' letterSpacing='0.01em' stroke="#4c5862" opacity="0.5">
                    {timeFrame}
                </text>
            </svg>
            <Box
                component="img"
                src={Defense}
                alt=""
                width={23}
                sx={{
                    position: 'absolute',
                    left: '50%',
                    top: '265px',
                    userSelect: 'none',
                    transform: 'translate(-50%, -50%)'
                }}
            />
            <Box sx={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                userSelect: "none",
                transform: 'translate(-50%, -50%)'
            }}>
                <Box sx={{
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    <Typography sx={{
                        fontSize: 65,
                        lineHeight: '65px',
                        fontFamily: 'Poppins-Regular',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}>
                        {Math.floor(time / 60)}
                    </Typography>
                    <Box>
                        <Typography sx={{
                            fontSize: 32,
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '36px',
                            textAlign: 'center',
                            letterSpacing: '0.01em',
                            color: '#192A3E',
                        }}>
                            :{time % 60}
                        </Typography>
                        <Typography sx={{
                            fontSize: 16,
                            fontFamily: 'Poppins-Regular',
                            lineHeight: '24px',
                            textAlign: 'right',
                            letterSpacing: '0.1em',
                            color: '#4C5862',
                            opacity: '0.5'
                        }}>
                            MIN
                        </Typography>
                    </Box>
                </Box>
                <Typography sx={{
                    fontSize: 16,
                    fontFamily: 'Poppins-Regular',
                    lineHeight: '24px',
                    textAlign: 'center',
                    letterSpacing: '0.1em',
                    color: '#4C5862',
                    opacity: '0.5',
                }}>
                    {(timeFrame * 3 / 4).toFixed(2)}
                </Typography>
            </Box>
        </Box>
    )
}

export default Timer;
