import React, { forwardRef } from 'react';
import { 
    Dialog,
    Paper,
    Slide,
    Typography,
    Box,
    Button,
} from '@mui/material';
import HunterImage from '../assets/images/hunter.svg';
import EnergyImage from '../assets/images/energy.png';
import LineProgressBar1 from './LineProgressBar1';
import LineProgressBar2 from './LineProgressBar2';

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const SleepingResultModal = ({open, onClose}) => {
    return (
        <Dialog 
            onClose={onClose} 
            open={open} 
            TransitionComponent={Transition}
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper sx={{
                p: '32px 24px 24px'
            }}>
                <Box 
                    sx={{maxWidth: '305px',
                    mx: 'auto'
                }}>
                    <Typography sx={{
                    fontSize: 15,
                    fontFamily: 'Poppins-Bold',
                    lineHeight: '22px',
                    textAlign: 'center',
                    letterSpacing: '0.01em',
                    color: '#192A3E',
                    mb: '24px'
                    }}>
                        Sleeping Result
                    </Typography>
                    <Typography sx={{
                        fontSize: 14,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '21px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        mb: '12px',
                        "& b": {
                            color: '#DF8494',
                        }
                    }}>
                        You won <b>+10%</b> of energy.
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        mb: '8px'
                    }}>
                        <Box sx={{
                            display: 'flex',
                            alignItems: 'center'
                        }}>
                            <img src={EnergyImage} alt="" />
                            <Typography sx={{
                                fontSize: 12,
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '18px',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                ml: '10px',
                                mr: '6px'
                            }}>
                                Energy
                            </Typography>
                        </Box>
                        <Typography>
                            <Typography
                                component="span"
                                sx={{ 
                                    fontSize: 10,
                                    fontFamily: 'Poppins-Regular',
                                    lineHeight: '15px',
                                    letterSpacing: '0.01em',
                                    color: '#DF8494',
                                    mr: '4px'
                                }}
                            >
                                +10%
                            </Typography>
                            <Typography
                                component="span"
                                sx={{ 
                                    fontSize: 10,
                                    fontFamily: 'Poppins-Regular',
                                    lineHeight: '15px',
                                    letterSpacing: '0.01em',
                                    color: '#4C5862',
                                    opacity: '0.5'
                                }}
                            >
                                (90%)
                            </Typography>
                        </Typography>
                    </Box>
                    <LineProgressBar1 value1={80} value2={90} color="#DF8494" />
                    <Typography sx={{
                        fontSize: 14,
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '21px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                        mt: '24px',
                        mb: '14px',
                        "& span": {
                            fontFamily: 'Poppins-Bold',
                            color: '#105157'
                        }
                    }}>
                        And You loose <span>-2</span> of hunter
                    </Typography>
                    <Box sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        mb: '8px'
                    }}>
                        <Box sx={{
                            display: 'flex',
                            alignItems: 'center'
                        }}>
                            <img src={HunterImage} alt="" />
                            <Typography sx={{
                                fontSize: 12,
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '18px',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                ml: '10px',
                                mr: '6px'
                            }}>
                                Hunter
                            </Typography>
                        </Box>
                        <Typography>
                            <Typography
                                component="span"
                                sx={{ 
                                    fontSize: 10,
                                    fontFamily: 'Poppins-Regular',
                                    lineHeight: '15px',
                                    letterSpacing: '0.01em',
                                    color: '#105157',
                                    mr: '4px'
                                }}
                            >
                                -2
                            </Typography>
                            <Typography
                                component="span"
                                sx={{ 
                                    fontSize: 10,
                                    fontFamily: 'Poppins-Regular',
                                    lineHeight: '15px',
                                    letterSpacing: '0.01em',
                                    color: '#4C5862',
                                    opacity: '0.5'
                                }}
                            >
                                (7)
                            </Typography>
                        </Typography>
                    </Box>
                    <LineProgressBar2 value1={70} value2={90} color="#105157" />
                </Box>
                <Button
                    variant='outlined'
                    fullWidth
                    onClick={onClose}
                    sx={{
                        color: '#CD1322',
                        minHeight: '37px',
                        fontFamily: "Poppins-SemiBold",
                        fontSize: '12px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        borderColor: '#CD1322',
                        mt: '24px',
                        "&:hover": {
                            borderColor: '#CD1322',
                        }
                    }}
                >
                    Close
                </Button>
            </Paper>
        </Dialog>
    );
}

export default SleepingResultModal;