import React, { useEffect, useState } from 'react';
import { makeStyles } from '@mui/styles';
import {
    AppBar,
    Box,
    Toolbar,
    Button,
    Drawer,
    Typography,
    IconButton,
    List,
    ListItem,
    ListItemText
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { useHistory } from 'react-router-dom';
import Web3 from 'web3';
import ArrowDownIcon from '../assets/images/arrow-down.svg';
import PelfImage from '../assets/images/pelf.png';

const useStyles = makeStyles(theme => ({
    toolBar: {
      backgroundColor: '#fff',
      minHeight: '64px!important'
    },
    menuButton: {
        minHeight: 64,
        color: "#1D1D1D!important",
        fontSize: '16px!important',
        fontFamily: "Poppins-Bold!important",
        borderRadius: '0!important',
        borderBottom: "4px solid transparent!important",
        textTransform: 'capitalize!important',
    },
    activeMenuButton: {
        borderBottomColor: "#CD1322!important",
    },
    activeMobildMenuButton: {
        backgroundColor: 'red!important'
    },
    walletButton: {
        background: '#BF0C1A!important',
        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)!important',
        fontFamily: 'Poppins-SemiBold!important',
        fontSize: '12px!important',
        letterSpacing: '0.01em!important',
        textTransform: 'capitalize!important',
        minHeight: '37px',
    },
}));

export default function ButtonAppBar() {
    const classes = useStyles();
    const history = useHistory();
    const [menu, setMenu] = useState(history.location.pathname.split('/')[1] || 'fight-club');
    const [drawerOpen, setDrawerOpen] = useState(false);
    const [account, setAccount] = useState('0');

    const toggleDrawer = (event, open) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
          return;
        }
        setDrawerOpen(open);
    };

    const ethEnabled = async () => {
		if (window.ethereum) {
            await window.ethereum.request({ method: 'eth_requestAccounts' });
            window.web3 = new Web3(window.ethereum);
            return true;
		}
		return false;
    }
    const clickConnectWallet = async () => {
		if (typeof window.web3 === 'undefined') {
		    return;
		}
        await ethEnabled()
        const accounts = await window.ethereum.request({ method: 'eth_accounts' });
        setAccount(accounts[0]);
    }

    useEffect(() => {
        const fetchAccount = async () => {
            const accounts = await window.ethereum.request({ method: 'eth_accounts' });
            setAccount(accounts[0]);
        }
        if (window.ethereum) {
            fetchAccount()
        }
    }, [])
    return (
        <Box sx={{ flexGrow: 1 }}>
            <AppBar position="fixed">
                <Toolbar className={classes.toolBar}>
                    <IconButton
                        onClick={(e) => toggleDrawer(e, true)}
                        sx={{
                            display: { xs: 'flex', md: 'none' }
                        }}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Box
                        sx={{
                            width: 126,
                            height: 38,
                            backgroundColor: 'rgba(5, 255, 0, 0.2)',
                            mr: { xs: '15px', md: '45px'}
                        }}
                    />
                    <Box sx={{
                        flexGrow: 1,
                    }}>
                        <Button
                            className={`${classes.menuButton} ${menu === 'fight-club' && classes.activeMenuButton}`}
                            sx={{ 
                                mr: '16px',
                                width: { xs: 122, lg: 138 },
                                display: { xs: 'none', md: 'inline-flex' }
                            }}
                            onClick={() => {
                                setMenu('fight-club');
                                history.push('/fight-club')
                            }}
                        >
                            Fight-Club
                        </Button>
                        <Button
                            className={`${classes.menuButton} ${menu === 'tournament' && classes.activeMenuButton}`}
                            sx={{ 
                                mr: '16px',
                                width: { xs: 122, lg: 138 },
                                display: { xs: 'none', md: 'inline-flex' }
                            }}
                            onClick={() => {
                                setMenu('tournament');
                                history.push('/tournament')
                            }}
                        >
                            Tournament
                        </Button>
                        <Button
                            className={`${classes.menuButton} ${menu === 'white-paper' && classes.activeMenuButton}`}
                            sx={{
                                width: { xs: 122, lg: 138 },
                                display: { xs: 'none', md: 'inline-flex' }
                            }}
                            onClick={() => {
                                setMenu('white-paper');
                                history.push('/white-paper')
                            }}
                        >
                            White Paper
                        </Button>
                    </Box>
                    <Box sx={{
                        display: 'flex',
                        alignItems: 'center',
                        mr: '20px'
                    }}>
                        <Typography sx={{
                            fontSize: 14,
                            letterSpacing: '0.01em',
                            fontFamily: 'Poppins-Bold',
                            color: '#1D1D1D',
                            mr: '4px'
                        }}>
                            {new Intl.NumberFormat('en-US').format(321510)}
                        </Typography>
                        <Box
                            component="img"
                            src={PelfImage}
                            alt=""
                            width={34}
                        />
                    </Box>
                    {account && <Button sx={{
                        display: 'flex',
                        alignItems: 'center',
                        textTransform: 'inherit',
                        color: '#BF0C1A'
                    }}>
                        <Box sx={{
                            marginRight: '12px'
                        }}>
                            <Typography sx={{
                                fontFamily: "Poppins-Bold",
                                fontSize: '14px',
                                lineHeight: '22px',
                                textAlign: 'right',
                                color: '#1D1D1D',
                            }}>
                                Roosters Club
                            </Typography>
                            <Typography sx={{
                                fontFamily: "Poppins-Medium",
                                fontSize: '14px',
                                lineHeight: '22px',
                                textAlign: 'right',
                                color: '#CD1322',
                            }}>
                                {account.slice(0, 6)}...{account.slice(account.length - 4)}
                            </Typography>
                        </Box>
                        <img src={ArrowDownIcon} alt="" />
                    </Button>}
                    {!account && <Button 
                        variant="contained"
                        className={classes.walletButton}
                        onClick={clickConnectWallet}
                    >
                        Connect Wallet
                    </Button>}
                    <Drawer
                        anchor="left"
                        open={drawerOpen}
                        onClose={(e) => toggleDrawer(e, false)}
                    >
                        <Box sx={{
                            minWidth: 280,
                            padding: '32px 0'
                        }}>
                            <List>
                                <ListItem
                                    as={Button}
                                    onClick={() => {
                                        setMenu('fight-club');
                                        history.push('/fight-club')
                                        setDrawerOpen(false)
                                    }}
                                    sx={{
                                        borderRadius: '0',
                                        textTransform: 'capitalize',
                                        backgroundColor: menu === 'fight-club' ? '#c7c7c7' : 'transparent',
                                        mb: '20px',
                                        "&:hover": {
                                            backgroundColor: '#c7c7c7'
                                        }
                                    }}
                                >
                                    <ListItemText primaryTypographyProps={{
                                        fontSize: '16px',
                                        fontFamily: "Poppins-Bold",
                                        color: "#1D1D1D",
                                    }}>
                                        Fight-Club
                                    </ListItemText>
                                </ListItem>
                                <ListItem
                                    as={Button}
                                    onClick={() => {
                                        setMenu('tournament');
                                        history.push('/tournament')
                                        setDrawerOpen(false)
                                    }}
                                    sx={{
                                        borderRadius: '0',
                                        textTransform: 'capitalize',
                                        backgroundColor: menu === 'tournament' ? '#c7c7c7' : 'transparent',
                                        mb: '20px',
                                        "&:hover": {
                                            backgroundColor: '#c7c7c7'
                                        }
                                    }}
                                >
                                    <ListItemText primaryTypographyProps={{
                                        fontSize: '16px',
                                        fontFamily: "Poppins-Bold",
                                        color: "#1D1D1D",
                                    }}>
                                        Tournament
                                    </ListItemText>
                                </ListItem>
                                <ListItem
                                    as={Button}
                                    onClick={() => {
                                        setMenu('white-paper');
                                        history.push('/white-paper')
                                        setDrawerOpen(false)
                                    }}
                                    sx={{
                                        borderRadius: '0',
                                        textTransform: 'capitalize',
                                        backgroundColor: menu === 'white-paper' ? '#c7c7c7' : 'transparent',
                                        "&:hover": {
                                            backgroundColor: '#c7c7c7'
                                        }
                                    }}
                                >
                                    <ListItemText primaryTypographyProps={{
                                        fontSize: '16px',
                                        fontFamily: "Poppins-Bold",
                                        color: "#1D1D1D",
                                    }}>
                                        White Paper
                                    </ListItemText>
                                </ListItem>
                            </List>
                        </Box>
                    </Drawer>
                </Toolbar>
            </AppBar>
        </Box>
    );
}