import React, { useState, useEffect } from 'react';
import { makeStyles } from '@mui/styles';
import {
    Box,
    Container,
    Tabs,
    Tab,
} from '@mui/material';
import { Switch, Route, useHistory, Redirect } from 'react-router-dom';
import Tab1 from './Tab1';
import Tab2 from './Tab2';
import Tab3 from './Tab3';
import Tab1Item from './Tab1Item';
import Tab2Item from './Tab2Item';
import Cashprice from '../../components/Cashprice';
import Tournament from '../../components/Tournament';
import Minting from '../../components/Minting';
import Battles from '../../components/Battles';

const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: '#91b08c',
        padding: '24px',
        marginTop: '64px'
    },
    tabs: {
        background: '#FFFFFF',
        boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
        borderRadius: '8px',
        color: '#CD1322',
        marginBottom: '20px'
    },
    tab: {
        color: '#192A3E!important',
        borderRadius: '8px!important',
        fontSize: '15px!important',
        fontFamily: 'Poppins-Medium!important',
        textTransform: 'capitalize!important'
    },
    activeTab1: {
        color: '#fff!important',
        backgroundColor: '#CD1322!important'
    },
    activeTab2: {
        color: '#fff!important',
        backgroundColor: '#DE7588!important'
    },
    activeTab3: {
        color: '#fff!important',
        backgroundColor: '#FEB12F!important'
    },
    tabPanels: {
        background: '#FFFFFF',
        boxShadow: '0px 6px 18px rgba(0, 0, 0, 0.06)',
        borderRadius: '8px',
    }
}));

const FightClub = ({match}) => {
    const classes = useStyles();
    const history = useHistory();
    const [value, setValue] = useState(0);

    useEffect(() => {
        switch(history.location.pathname.split('/')[2]) {
            case 'tab1':
                setValue(0)
                return
            case 'tab2':
                setValue(1)
                return
            case 'tab3':
                setValue(2)
                return
            default:
                setValue(0)
                return
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [history.location.pathname])

    const handleChange = (e, newValue) => {
        setValue(newValue);
    };

    return (
        <Box className={classes.root}>
            <Container sx={{
                display: 'flex',
                flexWrap: 'wrap'
            }}>
                <Box sx={{
                    maxWidth: { xs: '100%', md: '58%' },
                    width: { xs: '100%', md: '58%' },
                    mb: {xs: '24px', md: '0'},
                    mr: { md: '30px' }
                }}>
                    <Tabs
                        value={value}
                        onChange={handleChange}
                        className={classes.tabs}
                        variant="fullWidth"
                        >
                        <Tab 
                            className={`${classes.tab} ${value === 0 && classes.activeTab1}`}
                            label="Tab1"
                            onClick={() => history.push(`${match.path}/tab1`)}
                        />
                        <Tab 
                            className={`${classes.tab} ${value === 1 && classes.activeTab2}`}
                            label="Tab2"
                            onClick={() => history.push(`${match.path}/tab2`)}
                        />
                        <Tab 
                            className={`${classes.tab} ${value === 2 && classes.activeTab3}`}
                            label="Tab3 (2)"
                            onClick={() => history.push(`${match.path}/tab3`)}
                        />
                    </Tabs>
                    <Box className={classes.tabPanels}>
                        <Switch>
                            <Route exact path={`${match.path}/tab1/:id`} component={Tab1Item} />
                            <Route exact path={`${match.path}/tab1`} component={Tab1} />
                            <Route exact path={`${match.path}/tab2/:id`} component={Tab2Item} />
                            <Route exact path={`${match.path}/tab2`} component={Tab2} />
                            <Route exact path={`${match.path}/tab3`} component={Tab3} />
                            <Redirect from={`${match.path}`} to={`${match.path}/tab1`} />
                        </Switch>
                    </Box>
                </Box>
                <Box sx={{
                    maxWidth: { xs: '100%', md: 'calc(42% - 30px)' },
                }}>
                    <Cashprice />
                    <Minting />
                    <Battles />
                    <Tournament />
                </Box>
            </Container>
        </Box>
    )
}

export default FightClub;