import React, { forwardRef } from 'react';
import { 
    Dialog,
    Divider,
    Paper,
    Slide,
    Typography,
    Box
} from '@mui/material';

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="right" ref={ref} {...props} />;
});

const WaitingModal = ({open, onClose}) => {
    return (
        <Dialog 
            onClose={onClose} 
            open={open} 
            TransitionComponent={Transition}
            sx={{
                "& .MuiDialog-container > .MuiPaper-root": {
                    maxWidth: 460,
                    width: '100%'
                }
            }}
        >
            <Paper>
                <Typography sx={{
                    fontSize: 15,
                    fontFamily: 'Poppins-Medium',
                    lineHeight: '22px',
                    letterSpacing: '0.01em',
                    color: '#192A3E',
                    p: '16px 24px'
                }}>
                    Looking for opponent...
                </Typography>
                <Divider />
                <Box sx={{
                    p: '36px 50px 52px'
                }}>
                    <Box sx={{
                        backgroundColor: '#EAEAEA',
                        aspectRatio: '1',
                        width: 84,
                        mx: 'auto',
                        mb: '16px'
                    }}></Box>
                    <Typography sx={{
                        fontSize: 15,
                        fontFamily: 'Poppins-Bold',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '    ',
                        color: '#192A3E',
                        mb: '5px'
                    }}>
                        Research...
                    </Typography>
                    <Typography sx={{
                        fontSize: '14px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '21px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}>
                        Please wait, we are looking to find an opponent with the same rank
                    </Typography>
                </Box>
            </Paper>
        </Dialog>
    );
}

export default WaitingModal;